<template>
  <div class="body Abnormal">
    <div class="common-box">
      <header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>异常上报</span></header>
      <div v-if='!omsNo' class="content-box">
        <v-scan @scanData="scanData" :switchScan="switchScan"></v-scan>
      </div>
    </div>
    <div class="form-content">
      <div class="form-item">
        <label>订单号</label>
        <el-input class="form-input" type="text" v-model="OMS.omsNo" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label>运单号</label>
        <el-input class="form-input" type="text" v-model="OMS.deliveryNoSF" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label>处理承运商</label>
        <el-input class="form-input" type="text" v-model="$store.state.userInfo.orgnizationName" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label>发起人</label>
        <el-input type="text" v-model="$store.state.userInfo.userName" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label>异常类型</label><br>
        <el-select v-model="aboType" placeholder="异常类型选择">
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label>异常原因</label><br>
        <el-select v-model="aboReason" placeholder="异常原因选择">
          <el-option
            v-for="item in reasonOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label>异常环节</label><br>
        <el-select v-model="aboLink" placeholder="异常环节选择">
          <el-option
            v-for="item in linkOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label>责任方</label><br>
        <el-select v-model="aboPer" placeholder="责任方选择">
          <el-option
            v-for="item in perOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label>异常描述</label>
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="aboText">
        </el-input>
      </div>
    </div>
    <div class="uploadImg" >

      <div>上传图片<span>(限3张)</span>：</div>

      <el-upload  :action = " HOST + 'abnormalUploadImage.do'" 
                :auto-upload="false"  
                :data="uploadParams"  
                list-type="picture-card"  
                ref="upload" 
                :limit="3" 
                :multiple="true"
                :on-error="uploadErr" 
                 :show-file-list="true"
                :on-success="uploadSuccessful"    
                 :before-upload="beforeAvatarUpload"
                :on-preview="handlePicturePreview" >
        <i class="el-icon-plus"></i>
      </el-upload>
    
<!-- 
      <el-upload :action="HOST + 'reportAbnormal.do'"
                 ref="upload"
                 class="avatar-uploader"
                 :data="params"
                 name="fileupload"
                 :multiple="false"
                 :show-file-list="true"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload"
                 :on-change="getTheFile"
                 >
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload> -->
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>
    </div>
     
    <div class="footer-commit">
      <div class="commit" @click="commitAbo">提交</div>
    </div>
<!-- 
    <el-dialog title="上传图片"
               class="img-dialog"
               :visible.sync="commitImgDialog"
               :close-on-click-modal="false"
               :show-close="false"
               append-to-body width="90%">
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeImgInput">取 消</el-button>
        <el-button type="primary" @click.native="commitImg">确 定</el-button>
      </span>
    </el-dialog> -->
  </div>
</template>

<script>
  import Scan from '@/components/Scan'
  import Header from '@/components/header'
  import { getTheOrderNumber, abnormalDropList, commitAbnormal } from "../../../api/api";
  import { bus } from '../../../utils/bus'
  import Axios from "axios"
  import $ from "jquery"
  export default {
    name: "Abnormal",
    components: {
      'v-scan': Scan,
      'v-header': Header
    },
    data() {
      return {
        headerName: '上报异常',
        responseData: {},
        OMS: {},

        aboType: '', //异常类型
        typeOptions: [], // 异常类型下拉

        aboReason: '',
        reasonOptions: [],

        aboLink: '',
        linkOptions: [],

        aboPer: '',
        perOptions: [],

        aboText: '', // 异常描述

        // commitImgDialog: false,
        dialogImageUrl: '',//图片路径
        dialogVisible: false,//是否放大预览图片
        imgUrl: null,
        imgObj: null,
        uploadParams: {},//图片上传附带参数
        imageUrl: '',
        switchScan: true,
        uploadSuccessNum:0,//上传成功数量
        uploadTotalNum:0,//上传图片总数
        uploadFailNum:0,//上传失败数量
        shipmentCode:'',//配载单号
        omsNo:'',//订单号
        deliveryNoSF:'',//SF运单号
      }
    },
    mounted() {
      this.getAboList();
    },
    created(){
      
      var that = this;
      if(this.$route.query.shipmentCode){
        this.shipmentCode = this.$route.query.shipmentCode;
      }
      if(this.$route.query.omsNo){
        this.omsNo = this.$route.query.omsNo;
        
        this.OMS.omsNo = that.omsNo;
      }
      if(this.$route.query.deliveryNoSF){
        this.deliveryNoSF = this.$route.query.deliveryNoSF;
        
        this.OMS.deliveryNoSF = that.deliveryNoSF;
      }else{
        this.OMS.deliveryNoSF = "";
      }
      
    },
    methods: {
      // 上传前
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 10MB!');
        }
        return isLt2M;
      },
      // 文件上传成功时的钩子
      uploadSuccessful  (response, file, fileList){

          var that = this;
          console.log(response)
          if(response.status == 2){

            that.uploadSuccessNum = 0;
            that.uploadFailNum = 0;
            that.uploadTotalNum = 0;

            that.$emit('isLoading', false);

            this.$alert("异常上报成功，图片上传失败,"+response.Msg, '提示', {
                confirmButtonText: '确定',
                callback(){

                }
            })
            return;
          }


          this.uploadSuccessNum ++;

          if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
            // 全部上传成功

            that.finishUpload('');

            return;

          }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
            // 部分上传成功

            that.finishUpload('部分图片上传失败');
          }
      },
      // 文件上传失败时的钩子
      uploadErr  (err, file, fileList){
          console.log(err)
          var that = this;

          this.uploadFailNum++;

          if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
            // 全部上传失败

            that.uploadSuccessNum = 0;
            that.uploadFailNum = 0;
            that.uploadTotalNum = 0;
            that.$emit('isLoading', false);

            that.$alert("上传失败", '提示', {
              confirmButtonText: '确定'
            })
            return;
          }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
            // 部分上传失败

            that.finishUpload('部分图片上传失败');
          }
      },
      // 上传图片完成后（包括部分上传失败）
      finishUpload(text){

        var that = this;
        that.$emit('isLoading', false);

        var textvalue = '';
        if(text){
          textvalue = text?","+text:"";
        }
        
        this.$alert("异常上报成功"+textvalue, '提示', {
            confirmButtonText: '确定',
            callback(){

              that.$router.back(-1);
            }
        })
      },
      // 预览放大图片
      handlePicturePreview (file) {

          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
      },
      /**
       * 提交异常
       */
      commitAbo() {
        var that = this;

        // if(!$("div.uploadImg ul>li").length){

        //   this.$alert('请上传图片', '提示', {
        //     confirmButtonText: '确定'
        //   })

        //   return;
        // }

        this.uploadParams.omsNo = this.omsNo;//订单号
        this.uploadParams.fileType = 'wxAbnormal';//上传的类型 异常上传

        that.uploadTotalNum = $("div.uploadImg ul>li").length;
        console.log(that.uploadTotalNum )

        let params = {
          omsNo: this.OMS.omsNo,
          deliveryNoSF: this.OMS.deliveryNoSF,
          transporterId: this.$store.state.userInfo.orgnizationId,
          transporterName: this.$store.state.userInfo.orgnizationName,
          sponsorName: this.$store.state.userInfo.userName,
          abnormalType: this.aboType,
          abnormalReason: this.aboReason,
          abnormalLink: this.aboLink,
          responsibleParty: this.aboPer,
          abnormalExplain: this.aboText,
        }
        that.$emit('isLoading', true,"拼命加载中");
        
        Axios.post(that.HOST+"reportAbnormal.do",{"params": JSON.stringify(params)})
        .then(function(res){
          console.log(res)
          
          if(res.data.status=='1'){

            if(that.uploadTotalNum>0){
              that.$refs.upload.submit();
            }else{
              that.finishUpload();
            }

          }else{

            that.$emit('isLoading', false);

            var Msg;
            if(res.data.Msg){

              Msg = res.data.Msg;
            }else {

              Msg = "请求失败";
            }

            that.$alert(Msg, '提示', {
              confirmButtonText: '确定',
            })
          }
        })
        .catch(function(res){

          that.$emit('isLoading', false);
            
          that.$alert("请求失败", '提示', {
            confirmButtonText: '确定'
          })
        })
      },
      // 请求下拉框数据
      getAboList() {
        abnormalDropList().then(res => {
          console.log(res)
          this.typeOptions = [] // 异常类型
          this.reasonOptions = [] // 异常原因
          this.linkOptions = [] // 异常环节
          this.perOptions = [] // 责任方
          let obj = JSON.parse(res.data.data)

          for (let i in obj.abnormalLink) { // 异常环节
            let iitem = obj.abnormalLink[i]
            let iarr = {}
            iarr.value = iitem
            iarr.label = iitem

            this.linkOptions.push(iarr)
          }
          for (let j in obj.abnormalReason) { // 异常原因
            let jitem = obj.abnormalReason[j]
            let jarr = {}
            jarr.value = jitem
            jarr.label = jitem

            this.reasonOptions.push(jarr)
          }
          for (let k in obj.abnormalType) { // 异常类型
            let itemk = obj.abnormalType[k]
            let arrk = {}
            arrk.value = itemk
            arrk.label = itemk

            this.typeOptions.push(arrk)
          }
          for (let l in obj.responsibleParty) { // 责任方
            let iteml = obj.responsibleParty[l]
            let arrl = {}
            arrl.value = iteml
            arrl.label = iteml

            this.perOptions.push(arrl)
          }
        })
      },
      // 扫描后
      scanData(data) {

        var that = this;

        var postData = {
          scanLabel: data,//标签号
          shipmentCode: that.shipmentCode,//配载单号
        };

        console.log(postData)

        this.httpRequest("verificationOmsNo.do",postData,function(res){
          console.log(res);
          
          that.OMS={
            omsNo: res.data.omsNo,
            deliveryNoSF:res.data.deliveryNoSF
          }

          // let obj = res.data;

          // that.OMS = JSON.parse(obj.orderSF);
        })
      },
      goPrev() {
        this.$router.go(-1);
      },
    }
  }
</script>

<style scoped lang="less">
  @import "../../../components/publicLess.less";
.Abnormal{
  font-size: 28/50rem;
  padding-top: 90/50rem;
}
  .form-content{
    padding: 20px;
    background-color: #fff;
    position: relative;
  }
  .form-item {
    margin: 30/50rem 0;
    &>div{
      margin-top: 15/50rem;
    }
  }
  .form-item label {
    display: inline-block;
    font-size: 16px;
    color: #6f7180;
  }
  .footer-commit{
    font-size: 28/50rem;
    text-align: center;
    display: flex;
    width: 100%;
    color: #fff;
  }
  .footer-commit .commit{
    flex: 1;
    line-height: 70/50rem;
    margin: 0 30/50rem 30/50rem;
    background-color: @PublicColor;
    text-align: center;
    border-radius: 35/50rem;
  }
  .uploadImg{
    padding:0 30/50rem 30/50rem;
    box-sizing:border-box;
    div{
      float: none;
    }
    &>div:nth-child(1){
      margin-bottom: 15/50rem;
    }
    span{
      float: none;
      color: #aaa;
    }
  }
</style>



