<template>
	<div class="Index">
		<header><span>首页</span></header>
		<div class="container">
			<div class="swipper">
				<div class="block">
				    <el-carousel trigger="click" height="150px">
				        <el-carousel-item>
				        	<img style=" height:150px;width: 100%" src="../../assets/images/index1/ad_pic_2.jpg">
				        </el-carousel-item>
				        <el-carousel-item>
				        	<img style=" height:150px;width: 100%" src="../../assets/images/index1/ad_pic_1.png">
				        </el-carousel-item>
				       <!--  <el-carousel-item>
				        	<img style=" height:150px;width: 100%" src="../../assets/images/index1/search-bg.png">
				        </el-carousel-item>
				        <el-carousel-item>
				        	<img style=" height:150px;width: 100%" src="../../assets/images/index1/search-bg.png">
				        </el-carousel-item>
				        <el-carousel-item>
				        	<img style=" height:150px;width: 100%" src="../../assets/images/index1/search-bg.png">
				        </el-carousel-item> -->
				    </el-carousel>
				</div>
			</div>
			<div class='option'>
		        <router-link  to="/PrintExpress">
		          <i class="iconfont icon-dayin"></i>
		          <span>打印面单</span>
		        </router-link>
		        <router-link :to="{name:'TransitScan',query:{pagetype:'air'}}">
		          <i class="iconfont icon-saomiao"></i>
		          <span>航空出港扫描</span>
		        </router-link>
		        <router-link  to="/ExceptionList">
		          <i class="iconfont icon-qianshou"></i>  
		          <span>异常单签收</span>
		        </router-link>
		      </div>
			<div class="noticeHeader">通知：</div>
			<div class="noticeContent"  style="overflow:scroll;height: calc(100% - 310px);">
				<mt-loadmore style="text-align: center; "  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
					
					<div>
						<div class="noticeItem" @click="toWaybillInfo(itemData.shipmentID,itemData.appointLoadingSite)" v-for='(itemData,idx) in noticeList'  :id="idx"  :key='idx'>
							<div>
								<i v-if="itemData.daoDaPlace == '未查看'" class="iconfont icon-weiduxiaoxi  red" ></i>
								<i v-else class="iconfont icon-yiduxiaoxi-" ></i>
								<span>配载单号：</span><span>{{itemData.code}}</span>
								<span>{{itemData.addDate}}</span>
							</div>
						</div>
						<div v-if="allLoaded && noticeList.length " class="nodata_label">没有更多数据</div>
						<div class="kong"></div>
						<div v-if="is_NoData" class="nodata_kong"></div>
					</div>
				</mt-loadmore>
				<!-- 页面数据为空时 -->
				<div   v-if="is_NoData"   class="NoData" >
					<div>
						<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
						<div>没有通知</div>
					</div>
				</div>
			</div>
		</div>
		<FooterIndex/>
	</div>
</template>
<script type="text/javascript">
import FooterIndex from '../../components/footer.vue'
	export default{
		name:"Index",
		data(){
			return{
				noticeList:[],//通知列表
				numberPerPage: 10,//每页显示几条
				loadPage: 1 ,//当前第几页
				allLoaded:true,//上拉刷新还能上拉刷新
				autoFill:false,//
				is_NoData:false,//没有数据模块样式  是否显示
			}
		},
		components:{
		  	FooterIndex
		},
		mounted(){

			this.allLoaded = false;
		},
		created(){

			var that = this;

			this.loadData();

		},
		methods:{
			// 加载页面数据
			loadData(load){

				var that = this;

				// 请求通知列表数据
				var postData = {
					currentPage: this.loadPage,//当前第几页
					numberPerPage: this.numberPerPage,//每页显示几条
					cellphone: that.$store.state.userInfo.cellphone,//手机号
				}
				
				this.httpRequest( "deriverAueryShipmentData",postData,function(res){

					if(load){
						if(load == "TopLoad"){

							that.$refs.loadmore.onTopLoaded();

						}else if(load == "BottomLoad"){

							that.$refs.loadmore.onBottomLoaded();
						}
				    }

					//上拉加载后 数据拼接
			        if (that.loadPage > 1) {
				   
				        that.noticeList = that.noticeList.concat(res.data)
				      
			        } else {
				       
				        that.noticeList = res.data;

						if(res.data.length){

							that.is_NoData = false; //没有数据模块 是否显示

						}else{

							that.is_NoData = true;
						}
			        }

			        // 判断是否能上拉加载
				    if (res.data.length < that.numberPerPage) {
				 
				        that.allLoaded = true;
			
				    } else {
				        
				        that.allLoaded = false;
				      
				    }
				})
			},
			// 跳转到配载单信息页面
			toWaybillInfo(shipmentID,appointLoadingSite){

				this.$store.state.Waybill.WaybillInfo.clickShipmentId = shipmentID;
				
				// WEI   未交付    YI  已交付
				if(appointLoadingSite == "WEI"){

					this.$router.push({
						name:"WaybillInfo"
					})

				}else{

					this.$router.push({
						name:"WaybillDetail"
					})
				}
			},
			// 下拉刷新
			loadTop() {

		        this.loadPage = 1;

		        this.allLoaded = false;

				this.loadData("TopLoad");
			},
			// 上拉加载
			loadBottom(){

			    var that = this;

			    if (that.allLoaded){
			     	that.$refs.loadmore.onBottomLoaded();
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				this.loadData("BottomLoad");
			}
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
	.Index{
		height: 100%;
		overflow: hidden;
		.container{
			height: 100%;
			overflow: scroll;
			.option{
			    width:100%;
			    overflow: hidden;
			    background-color: #F4F4F4;
			    &>a{
			      width:33%;
			      height:190/50rem;
			      float: left;
			      line-height: 65/50rem;
			      margin-right: 0.5%;
			      margin-bottom: 0.5%;
			      background-color: #fff;
			      text-align: center;
			      color: #000;
			      i{
			        font-size:60/50rem;
			        color: @PublicColor;
			        display: block;
			        margin-top: 45/50rem;
			        &.icon-saomiao{
			          font-size: 55/50rem;
			        }
			      }
			      &:nth-child(3),&:nth-child(6){
			        margin-right:0;
			      }
			      &>span{
			        font-size: 28/50rem;
			      }
			    }
			  }
			.noticeHeader{
				background-color: #f0f0f0;
				height: 40px;
				line-height: 40px;
				padding:0 20/50rem;
				box-sizing: border-box;
				color: #999;
			}
			.noticeContent{
				font-size: 28/50rem;
				overflow: scroll;
				div.noticeItem{
					&>div{
						overflow: hidden;
						height: 100/50rem;
						line-height: 100/50rem;
						border-bottom: 5/50rem solid #EDEFF3;
						i{
							font-size: 60/50rem;
							width: 60/50rem;
							float: left;
							margin-right: 10/50rem;
							color: #777;
							text-align: center;
						}
						i.red{
							color: red;
						}
						i.icon-yiduxiaoxi-{
							font-size: 48/50rem;
						}
						&>span:nth-child(2),&>span:nth-child(3),{
							float: left;
						}
						&>span:nth-child(4){
							float: right;
							color: #333;
							font-size: 26/50rem;
						}
					}
				}
			}
		}
	}
</style>


