<template>
	<div class="WaybillInfo">
		<header><i class="iconfont icon-xiangzuo1"   @click="goprev"></i><span>配载单信息</span></header>
		<div class="container">
			<div class="waybillContainer">

				<div  class="bigListBox" >

					<!-- 配载单信息 -->
					<WaybillInfo_template  @isLoading_="fullscreenLoading"   v-bind:shipmentListData="shipmentListData" v-bind:LinkStatus='LinkStatus' />

					<!-- 订单信息 -->
					<OrderInfo_template v-if="orderList.length" @isLoading_="fullscreenLoading"   v-bind:_orderInfo="orderList,shipmentID,backBottleFlag,confirmNumOfDriverReceive,shipStatus" v-bind:LinkStatus='LinkStatus' />
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

	import $ from "jquery"

	// 配载单信息 组件
	import WaybillInfo_template from '../_template_WaybillInfo'

	// 订单信息 组件
	import OrderInfo_template from "./_template_orderInfo"
	export default{
		name:"WaybillInfo",
		
		data(){
			return{
				shipmentID:"",//配载单id
				shipmentListData:[],//配载单信息
				orderList:[],//订单列表
				shipStatus:"",//配载单状态
				backBottleFlag:"F",//回瓶标志
				confirmNumOfDriverReceive:"F",//司机确认收货信息状态
				LinkStatus:'Link_PickUp',//环节
			}
		},
		components:{
			OrderInfo_template,// 订单信息 组件
			WaybillInfo_template,//配载单信息组件
		},
		created(){

			var that = this;

			// 当前环节
			if(this.$route.query.LinkStatus){
				this.LinkStatus = this.$route.query.LinkStatus;
			}

			this.shipmentID = this.$store.state.Waybill.WaybillInfo.clickShipmentId;
			
			this.loadData();
		},
		methods:{
			// 返回上一页
			goprev(){

				var that = this;

				this.$router.push({
					name:"Waybill",
					query:{
						LinkStatus:that.LinkStatus
					}
				})

			},
			loadData(){
				var that = this;
				
				var postData = {
					shipmentId: that.shipmentID,//配载单id
			    };

				this.httpRequest("queryDeliverDetailInfo.do",postData,function(res){

					if(res.data.length){

						if(res.data[0].length){

							that.shipmentListData = res.data[0];//配载单信息

							that.$store.state.Waybill.WaybillInfo.clickWaybillInfo =  res.data[0];

						}else{
							// 没有配载信息

							that.shipmentListData =that.$store.state.Waybill.WaybillInfo.clickWaybillInfo;
						}

						// 配载单状态
						that.shipStatus = that.shipmentListData[0].status;

						that.backBottleFlag = that.shipmentListData[0].backBottleFlag;//回瓶标志
						that.confirmNumOfDriverReceive = that.shipmentListData[0].confirmNumOfDriverReceive;///司机确认收货信息状态

						that.orderList = res.data[1];//订单列表

						if(res.data[1].length){

							if(that.shipmentListData[0].status == "新建"){

								that.orderList[0].notOperate = true;

							}else{
								that.orderList[0].notOperate = false;
							}
							
							for(var m = 0; m < res.data[1].length; m++){

								// 判断订单是否可以进行交付动作
								if( res.data[1][m].status == "未交付" && that.shipmentListData[0].status != "新建"){
									// 可交付
									that.orderList[m].ifDeliver = true;

								}else{
									// 不可交付
									that.orderList[m].ifDeliver = false;
								}
							}
						}
						
					}else{

						//不需要弹出框提示，页面显示“没有数据”

					}
				})
			},
			//导航地址出现  加载特效出现  子传父事件 
			"fullscreenLoading"(isLoad,loadingText){

				console.log(isLoad,loadingText)

				this.$emit('isLoading', isLoad,loadingText);

			},
			// 点击导航按钮
			Navigation(idx){
				event.stopPropagation();

				var that = this;

				this.Navigate_addressList = [];

				// 装货地址列表
				var loading_addressList = that.bigListData[idx].issueAddrLiat;

				// 送货地址列表
				var Delivery_List = that.bigListData[idx].deliveryList

				var Delivery_addressList = [];
				for(var m = 0; m < Delivery_List.length; m++){

					Delivery_addressList[m] = {
						issueAddr1: Delivery_List[m].recAddr1,//地址
						issuePhone: Delivery_List[m].recPhone,//电话
						issueUser: Delivery_List[m].recName,//联系人
					}
				}

				that.Navigate_addressList = loading_addressList.concat(Delivery_addressList);

				if(that.Navigate_addressList.length){

					that.typeMShow = true;
				}else{

					that.$alert("没有要导航的地址信息", '提示', {
			            confirmButtonText: '确定',
			            callback: action => {
				            
			            }
		           	})
				}
			},
			// 选择具体要 导航的地址
			Navigate_SelectComit(Navigate_detailAddress){

				// 选择的导航具体地址
				var  Navigate_detailAddress= Navigate_detailAddress;

				console.log(Navigate_detailAddress);

				this.typeMShow = false;

				// 安卓
				try {
					CallAndroidOrIOS.callAndroid("导航", Navigate_detailAddress);
				} 
				catch(error) {
					console.log("没有CallAndroidOrIOS.callAndroid方法")
				}
				// 苹果
				try {
					CallAndroidOrIOS("导航", Navigate_detailAddress);
				}
				catch(error) {
					console.log("没有CallAndroidOrIOS方法")
				}
			},
			// 跳转到 装货预约 页面
			LoadingAppoint:function(idx){
				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"LoadingAppoint",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 到达确认 页面
			toArrivalConfirm:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"ArrivalConfirm",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 途中反馈 页面
			toWayFeedback:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"WayFeedback",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
			// 跳转到 发车确认 页面
			toDepartureConfirm:function(idx){

				event.stopPropagation();

				var that = this;

				this.$router.push({
					name:"DepartureConfirm",
					query:{
						StowageList : that.bigListData[idx]
					}
				})
			},
		}
	}
</script>
<style lang="less" scoped>
.WaybillInfo{
	overflow: hidden;
}
.bigListBox{
	text-align: left;
	// margin-bottom: 150/50rem;
}
.bigList{
	border-radius: 0;
}
</style>


