<template>
    <div class="Search">
	    <header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span>搜索</span></header>
		<div class="container">
  			<div class='CompetitionScreen'>
    			<div class='screen'>
      			    <div><span>开始日期</span><el-date-picker id="startvalue"  :editable='false' v-model="startvalue"  type="date"  placeholder="选择日期"></el-date-picker></div>
      			    <div><span>结束日期</span><el-date-picker id="endValue"  :editable='false' v-model="endValue"  type="date"  placeholder="选择日期"></el-date-picker></div>
                    
                    <div>
                        <span> 订&nbsp;单&nbsp;号</span>
                        <input v-model='orderNum' placeholder='请输入订单号'  ></input>
                        <i v-if="orderNum" @click="clearInput('orderNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    <div>
                        <span>配载单号</span>
                        <input v-model='order_WaybillNum' placeholder='请输入配载单号'  ></input>
                        <i v-if="order_WaybillNum" @click="clearInput('order_WaybillNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    <div>
                        <span>运输环节</span>
                        <el-select v-model="TransportLink" placeholder="请选择运输环节">
                            <el-option
                              v-for="item in TsptLinkList"
                              :key="item.value"
                              :label="item.label"
                              :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
		        </div>
                <div class="g_doubleBtn">
                    <div @click="reset">重置</div>
                    <div @click="sureScreen">搜索</div>
                </div>
		    </div>
	    </div>
	</div>
</template>
<script type="text/javascript">
    
    import $ from "jquery"
	export default{
		name:"Search",
		data(){
			return{
                startvalue:null,//开始时间
                endValue:null,//结束时间

                orderNum:null,//订单号
                order_WaybillNum: null,//配载单号
                TsptLinkList: [{
                  value: '提货',
                  label: '提货'
                }, {
                  value: '中转',
                  label: '中转'
                }, {
                  value: '派件',
                  label: '派件'
                }, {
                  value: '签收',
                  label: '签收'
                }],
                TransportLink: '',//运输环节
			}
		},
        created(){

            var that = this;

            this.startvalue =  that.$store.state.history_SearchData.startDate;
            this.endValue =  that.$store.state.history_SearchData.endDate;
            this.orderNum =  that.$store.state.history_SearchData.orderNoinput;//订单号
            this.order_WaybillNum =  that.$store.state.history_SearchData.shipmentCodeinput;//配载单号
            this.TransportLink =  that.$store.state.history_SearchData.TransportLink;//运输环节
  
        },
    	methods:{
            goPrev(){
                var that = this;

                // 不需要重新请求数据
                this.$store.state.history_needRefresh = false;

                this.$router.push({
                    name:"historyList",
                    query:{
                    }
                })
            },
    		// 点击 筛选 按钮
            sureScreen(){

                var that = this;

                var startDate,endDate;

                if($("#startvalue").val()){

                    startDate = $("#startvalue").val();

                }else{
                    startDate =null;
                }

                if($("#endValue").val()){

                    endDate = $("#endValue").val();

                }else{
                    endDate =null;
                }

                var LinkStatus = this.$store.state.history_LinkStatus;

                that.$store.state.history_SearchData = {
                    startDate: startDate,// 开始日期
                    endDate: endDate,//  结束日期
                    orderNoinput: that.orderNum,// 订单号
                    shipmentCodeinput: that.order_WaybillNum,//  配载单号
                    TransportLink: that.TransportLink,//运输环节
                    cellphone: that.$store.state.userInfo.cellphone,//手机号
                    transportLinks:LinkStatus == "Link_PickUp" ? "提货":(LinkStatus == "Link_Transit" ? "中转":(LinkStatus == "Link_ToSend" ? "派件":""))
                }

                console.log(that.$store.state.history_SearchData)

                that.$store.state.history_NoSearchFrom = false;//判断是否为搜索页面转来 false为搜索页面转来

                that.$router.push({
                    name:"historyList",
                    query:{
                       
                    }
                })
            },
            // 重置按钮
            reset(){
                this.startvalue = null;//开始时间
                this.endValue = null;//结束时间

                // 历史任务
                this.orderNum = null;//订单号
                this.order_WaybillNum =  null;//配载单号
                this.TransportLink = null;//运输环节
            },
    	}	
    }
</script>
<style lang="less" scoped>
.Search{
    overflow: hidden;
}
  	.container{
        overflow: hidden;
    	.CompetitionScreen{
            height: 100%;
            font-size: 28/50rem;
            &>.screen{
                background-color: #fff;
                &>div{
                    border-bottom: 1/50rem solid #EDEDED;
                    padding:0 20/50rem;
                    box-sizing: border-box;
                    position: relative;
                    height: 95/50rem;
                    line-height: 95/50rem;
                    &>span{
                        width: 150/50rem;
                        float: left;
                    }
                    &>input{
                        float: left;
                        border: none;
                        width:500/50rem;
                        height: 92/50rem;
                    }
                    .icon-iconfontcuowu{
                        right:50/50rem;
                        top: 4/50rem;
                    }
                }
            }
        }
  	}
    @media screen and (max-width: 374px){

      .container{
        .CompetitionScreen{
            &>.screen{
                &>div{
                  border-bottom: 3/50rem solid #EDEDED;
                }
            }
        }
      }
    }
</style>


