<template>
  <div class="TrackOnTheWay">
    <header><i class="iconfont icon-xiangzuo1"   @click="$router.back(-1)"></i><span>{{headerText}}</span></header>
    <div class="container" id="container"> </div>
    <div id="panel"></div>
    <div v-if="ifTips" class="msg_tips_text">
      <div class="tips_content">
        <p>{{tips_Msg}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">

  import $ from "jquery"
  import Axios from "axios"
  export default{
    data(){
      return{
        headerText:"途中轨迹", //页面标题
        shipmentID:'',//运单id
        pointsArray:[],//轨迹点集合
        lngLats:[],
        lineArr:[],
        map_g:"",
        myInterval:"", // 等待标注计时器
        myInterval_onComplete:"", // 获取位置成功时，等待画线计时器
        myInterval_onError:"", // 获取位置失败时，等待画线计时器
        geoShopList:[], // 需要地址转坐标的门店
        geoTotalQty:0, // 有多少数量需要地址转坐标
        geoOkQty:0, // 完成多少个地址转坐标
        ifTips:false,
        tips_Msg:"",
      }
    },
    mounted(){
      var that = this

      if(this.$route.query.head){
        this.headerText = this.$route.query.head;
      }
      if(this.$route.query.shipmentID){
        this.shipmentID = this.$route.query.shipmentID;
      }else{

        that.$alert("运单id为空", '提示', {
          confirmButtonText: '确定',
          callback(){
            that.$router.back(-1);
          }
        })
        return;
      }

      // 加载地图
      var map = new AMap.Map('container', {
        resizeEnable: true,
      })
      that.map_g = map

      // AMap.plugin('AMap.Driving', function() {
      //   var driving = new AMap.Driving({
      //     // 驾车路线规划策略，AMap.DrivingPolicy.LEAST_TIME是最快捷模式
      //     policy: AMap.DrivingPolicy.LEAST_TIME
      //   })
        
      //   var startLngLat = [116.379028, 39.865042]
      //   var endLngLat = [116.427281, 39.903719]
        
      //   driving.search(startLngLat, endLngLat, function (status, result) {
      //     // 未出错时，result即是对应的路线规划方案
      //     console.log(status)
      //     console.log(result)
      //   })
      // })

      var postData = {
        shipmentId: that.shipmentID,//运单id
      };

      that.$emit('isLoading', true,"拼命加载中");
      
      Axios.post(that.HOST+"getPathData.do",postData)
      .then(function(res){
        console.log(res)

        that.$emit('isLoading', false);
        
        if(res.data.States==1){

          that.pointsArray = res.data;

        }else{

          var Msg;
          if(res.data.Msg){

            Msg = res.data.Msg;
          }else {

            Msg = "请求失败";
          }

          that.$alert(Msg, '提示', {
            confirmButtonText: '确定',
          })
        }
      })
      .catch(function(res){

        that.$emit('isLoading', false);
          
        that.$alert("请求失败", '提示', {
          confirmButtonText: '确定'
        })
      })
    },
    created(){

    },
    methods:{
    }
  }
</script>
<style lang="less" scoped>
  .TrackOnTheWay{
    width: 100%; 
    height: 100%;
    font-size: 33/50rem;
    .container{
      width: 100%; 
      height: 100%;
    }
    #panel {
      position: fixed;
      background-color: white;
      max-height: 90%;
      overflow-y: auto;
      top: 10px;
      right: 10px;
      width: 280px;
      margin-top: 80/50rem;
      visibility: hidden;
    }
    #panel .amap-lib-driving {
      border-radius: 4px;
      overflow: hidden;
    }
  }
</style>


