<!-- _template_orderInfo.vue   订单信息 组件-->
<template>
    <div class="OrderInfo_template">
		<div class="orderHead" >
			<!-- <span class="selectAll" v-if="!historyList&&(_orderInfo[0]? !_orderInfo[0].notOperate : true)"  @click="selectAll">取消全选</span> -->
		订单信息</div>
		<div class='goodsInfo'   v-for='(dataItem,index) in _orderInfo'  :id="index"  :key='index' >
			<!--  @click="selectData(index,dataItem.ifDeliver)" -->
		    <div class='goodsHead'>
		    	<!-- <span v-if="dataItem.ifDeliver" class="iconfont icon-xuanzhong"></span> -->
		    	<span v-if=" LinkStatus=='Link_PickUp'" @click='PrintExpress(index)' class="iconfont icon-dayin"><span>打印</span></span>

				<div class="KeyInfoGroup" v-if=" LinkStatus=='Link_ToSend'" >
					<div v-if='chargeTypes=="到付"'><span>到付</span></div>
					<div v-if='returnBillFlag=="T"'><span>签回单</span></div>
				</div>

		    	<div class="ordernum">
		    		<span>订单号：</span>
		    		<span v-clipboard:copy="dataItem.omsNo"
				          v-clipboard:success="onCopy"
				          v-clipboard:error="onError">{{dataItem.omsNo}}
				      <i  v-clipboard:copy="dataItem.omsNo"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>
				    </span>
		    	</div>
		    	<div>
		    		<span>SF运单号：</span>
		    		<span v-clipboard:copy="dataItem.clientOrderNo"
				          v-clipboard:success="onCopy"
				          v-clipboard:error="onError">{{dataItem.clientOrderNo}}
				      <i v-if='dataItem.clientOrderNo'  v-clipboard:copy="dataItem.clientOrderNo"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>
				    </span>
		    	</div>
		    	<!-- backBottleFlag 为 T 时，是回瓶单 -->
		    <!-- 	<div v-if="backBottleFlag != 'T' || dataItem.receivePartyName">
		    		<span>收货单位：</span>
		    		<span>{{dataItem.receivePartyName}}</span>
		    	</div> -->
		    	<div v-if="backBottleFlag != 'T' || (dataItem.receivePartyContactName || dataItem.receivePartyPhone)">
		    		<span>收货联系人：</span>
		    		<span>{{dataItem.receivePartyContactName}}&nbsp;&nbsp;&nbsp; {{dataItem.receivePartyPhone}}&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i @click="callPhone(dataItem.receivePartyPhone)" class="iconfont icon-dianhua-copy"></i></span>
		    	</div>
		    	<div v-if="backBottleFlag != 'T' || dataItem.receivePartyAddr1">
		    		<span>收货地址：</span>
		    		<span>{{dataItem.receivePartyAddr1}}</span>
		    	</div>
		    <!-- 	<div>
		    		<span>装货单位：</span>
		    		<span>{{dataItem.issuePartyName}}</span>
		    	</div> -->
		    	<div>
		    		<span>装货联系人：</span>
		    		<span>{{dataItem.issuePartyContact}}&nbsp;&nbsp;&nbsp; {{dataItem.issuePartyTel}}&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i v-if="dataItem.issuePartyTel" @click="callPhone(dataItem.issuePartyTel)" class="iconfont icon-dianhua-copy"></i></span>
		    	</div>
		    	<div>
		    		<span>装货地址：</span>
		    		<span>{{dataItem.issuePartyAddr}}</span>
		    	</div>
		<!--     	<div   v-if="dataItem.send">
			派送要求字段没有值时，不在页面显示
					    	<span>派送要求：</span>
			<span>{{dataItem.send}}</span>
		</div>
		<div  v-if="dataItem.requestedDeliveryDte">
			<span>要求交付时间：</span>
			<span>{{dataItem.requestedDeliveryDte}}</span>
		</div> -->
		    	<div>
		    		<div v-if="LinkStatus=='Link_PickUp'">
			    		<span>件数：</span>
			    		<span>{{dataItem.actualQty}}</span>
			    	</div>
			    	<div v-if="LinkStatus=='Link_PickUp'">
			    		<span>重量：</span>
			    		<span>{{dataItem.actualWt}} &nbsp; KG</span>
			    	</div>
		    	</div>
		    	<div><span>保价金额：</span><span>{{dataItem.valuationMoney}} &nbsp;元</span></div>
		    	<div v-if='dataItem.loadingStatus == "WEI" && dataItem.scanNumber'><span>已扫描：</span><span>{{dataItem.scanNumber}} &nbsp;件</span></div>
		    	<!-- <div><span>状态：</span><span>{{dataItem.loadingStatus == 'YI'?已提交:()}}</span></div> -->
		    </div>

	    	<div v-if='!historyList' class="_btn">

	    		<!-- 修改订单 提货 -->
	    		<span v-if="LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI'"  @click="ChangeOrderInfo(index)" class="g_btn">修改订单</span>


		    	<!-- 提货扫描 提货 -->
	    		<span v-if="(LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toDeliveryScanXH(index)" class="g_btn">提货扫描</span>
				
				<!-- 提货扫描 提货 -->
	    		<span v-if="(LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)"  class="g_btn g_btn_disab">提货扫描</span>

				
	    		<!-- 装车扫描 中转 -->
	    		<span v-if="(LinkStatus=='Link_Transit' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toTransitScan(index)" class="g_btn">装车扫描</span>

	    		<!-- 装车扫描 中转 -->
	    		<span v-if="(LinkStatus=='Link_Transit' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)"  class="g_btn g_btn_disab">装车扫描</span>

				<!-- 提交装车 派件 -->
	    		<span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI'"  @click="submitOrders(index,'装车')">提交装车</span>

	    		<!-- 装车扫描 派件 -->
	    		<span v-if="(LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toDeliveryOrderPickScan(index)" class="g_btn">装车扫描</span>

	    		<!-- 装车扫描 派件 -->
	    		<span v-if="(LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)" class="g_btn g_btn_disab">装车扫描</span>

	    		<!-- 签收扫描 签收 -->
	    		<span v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && dataItem.signStatus == 'WEI'"  @click="toReceiptScan(index)" class="g_btn">签收扫描</span>

	    		<!-- 提交签收 签收 -->
	    		<!-- <span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && dataItem.signStatus == 'WEI'"  @click="submitOrders(index,'签收')">提交签收</span> -->


		    	<!-- 异常上报 提货、派件、签收 -->
		    	<span class="g_btn" @click="toAbnormal(index)">异常上报</span>


				<!-- 提交订单 提货、中转 -->
		    	<span class="g_btn" v-if="(LinkStatus=='Link_PickUp'||LinkStatus == 'Link_Transit')&& dataItem.loadingStatus == 'WEI'" @click="submitOrders(index,'订单')">提交订单</span>


	    		<!-- 签单上传 签收 -->
	    		<span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && returnBillFlag=='T'"  @click="toMassTransfer(index)">签单上传</span>
	    	</div>
		    	
		    <div class='goodsContent' @click="detail_takaUp(index)" :style="{display: backBottleFlag == 'T' ? 'block' : 'none'}">
			    <div v-for='(Item,idx) in dataItem.deliveryDetailList' :id="idx" :key='idx'>{{Item.productNo}} &nbsp;&nbsp;&nbsp;  {{Item.productName}}&nbsp;&nbsp;&nbsp;      {{Item.poOrderQty}}{{Item.poUom}}</div>
		    </div>
		    <div class="goodsBtn" v-if='dataItem.deliveryDetailList.length'>
		    	<div class="TakeUp" @click="detail_takaUp(index)">
			    	<div><span>{{backBottleFlag == 'T' ? '收起明细' : '查看货物信息'}}</span><i  class="iconfont icon-up" :style="{transform : backBottleFlag == 'T' ? 'rotate(180deg)' : 'rotate(0deg)'}"></i></div>
			    </div>
		    	<!-- <span v-if="dataItem.ifDeliver" class="g_btn" @click="toOrderDeliver(index)">交付</span>
		    	<span v-if="!historyList"  @click="_ewm(dataItem.omsNo)" class="g_btn ewm">二维码</span> -->
		    </div>
		   <!--  <div v-if=" (_orderInfo[0]? !_orderInfo[0].notOperate : true) && !historyList"  class="g_doubleBtn bottomBtn">
		   	            <div v-if="!selectAddr" @click="toSelectAdd('ArriveReceivingPoint')">到达收货点</div>
		   	            <div @click="toOrderDeliver('selectAll')">批量交付</div>
		   	            <div v-if="!selectAddr" @click="toSelectAdd('deliveryAddress')">按地址交付</div>
		   	        </div> -->
		</div>
		<div v-if="ifTips" class="msg_tips msg_tips1">
			<div class="tips_content">
				<i class="iconfont icon-chenggong1"></i>
				<span>{{tips_Msg}}</span>
			</div>
		</div>

	    <!-- 修改订单弹出框 -->
	    <div class="BatchCommitPopBox" v-show='ChangeInfo_show'>
	      <div class="BatchCommit_">
	        <div class="BatchCommit_head">修改订单信息</div>
	        <div class="BatchCommit_ChangeInfo">
	          <div>
	          	<div><span>件数</span></div>
	          	<div><input type="number" v-model='ChangeOrderQty'></div>
	          	<div><span></span></div>
	          </div>
	          <div>
	          	<div><span>重量</span></div>
	          	<div><input type="number" v-model='ChangeOrderWt'></div>
	          	<div><span>KG</span></div>
	          </div>
	        </div>
	        <div class="BatchCommit_btn">
	          <div class="commit-button" @click='ChangeInfoCancel'>取消</div>
	          <div class="commit-button" @click='ChangeInfoConfirm'>确定</div>
	        </div>
	      </div>
	    </div>
    </div>
</template>

<script>
	import $ from "jquery"

export default {
    name:"OrderInfo_template",
    props:{
		_orderInfo:{
    		type:Array,
	    	default: () => {
				return []
			}
    	},
    	shipmentID:0,
    	backBottleFlag:'',//回瓶标志
    	shipStatus:'',//配载单状态
    	confirmNumOfDriverReceive:'',//司机确认收货信息状态
    	selectAddr:false,//是否是选择地址页面跳转来
    	historyList:false,//是否是历史任务页面跳转来
		LinkStatus:'',//当前环节

    },// 用来接收父组件传给子组件的数据
    data(){
	    return{
	    	// _orderInfo:[],//订单信息列表

			tips_Msg:"",//提示信息
			ifTips:false,
			ChangeInfo_show:false,//修改订单 弹出框 是否显示
			ChangeOrderIndex:0,//修改订单 当前点击的订单的idx
			ChangeOrderQty:'',//修改订单 当前点击的订单的件数
			ChangeOrderWt:'',//修改订单 当前点击的订单的重量
	    }
    },
    mounted(){

    },
    created(){
    	console.log(this.LinkStatus)
    },
    methods:{
    	// 打印面单
    	PrintExpress(index){
    		var that = this;

    		var postData = {
				omsNo: that._orderInfo[index].omsNo,//标签号
		    };
		    console.log(postData)

			this.httpRequest("surfacePrint.do",postData,function(res){
				console.log(res);

				var omsNo =  JSON.stringify(res.data);

				try{
	    			var main = plus.android.runtimeMainActivity();
		            var Intent = plus.android.importClass("android.content.Intent");
		            var intent = new Intent(main.getIntent());
		            intent.setClassName(main, "com.kaidongyuan.app.kdydriver.ui.activity.PrintActivity");
		            intent.setFlags(Intent.FLAG_ACTIVITY_CLEAR_TOP);
		            intent.putExtra("omsNo", omsNo);
		            main.startActivity(intent);
	    		}
	    		catch(error) {
	    			alert(error)
				}
			},'isLoading_')
    	},
		// 跳转到 提货扫描 页面
		toDeliveryScanXH (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"DeliveryScanXH",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					transportLinks:that.LinkStatus,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		// 修改订单 提货
		ChangeOrderInfo(index){

		  this.ChangeOrderIndex = index;//修改订单 当前点击的订单的idx

		  this.ChangeOrderQty = this._orderInfo[index].actualQty,//修改订单 当前点击的订单的件数
		  this.ChangeOrderWt = this._orderInfo[index].actualWt,//修改订单 当前点击的订单的重量
		  this.ChangeInfo_show = true;
		},
		// 修改订单 取消按钮
		ChangeInfoCancel(){
			this.ChangeInfo_show = false;
		},
		// 修改订单 确定按钮
		ChangeInfoConfirm(){

			var that = this;

			if(!that.ChangeOrderQty || !that.ChangeOrderWt){
				return;
			}

			that.ChangeInfo_show = false;
	        var postData = {
	        	omsNo: that._orderInfo[that.ChangeOrderIndex].omsNo,//订单号
	            deliveryId: that._orderInfo[that.ChangeOrderIndex].deliveryId,//运单id
	            orderQty: that.ChangeOrderQty,//修改后的数量
	            orderWt: that.ChangeOrderWt,//修改后的重量
	        };

	        console.log(postData)

	        that.httpRequest("updateOrderQty.do",postData,function(res){
	          console.log(res)

	          that.$alert('修改订单成功', '提示', {
	            confirmButtonText: '确定',
	            callback(){
	              that.$parent.loadData();
	            }
	          })
	        },'isLoading_')
		},
		// 提交订单
		submitOrders(index,text){
			event.stopPropagation();
			var that = this;

			this.$confirm("确认要提交"+text+"吗？ 订单号："+ that._orderInfo[index].omsNo , '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {

		        var linkNode = ''; // 需要传参的linkNode类别

		        if(that.LinkStatus == 'Link_PickUp'){
		        	linkNode = '10';
		        }
		        if(that.LinkStatus == 'Link_Transit'){
		        	linkNode = '20';
		        }
		        if(that.LinkStatus == 'Link_ToSend' && that._orderInfo[index].loadingStatus == 'WEI'){
		        	linkNode = '30';
		        }
		        if(that.LinkStatus == 'Link_ToSend' && that._orderInfo[index].loadingStatus == 'YI'){
		        	linkNode = '40';
		        }

	        	var postData = {
					deliveryId:that._orderInfo[index].deliveryId,//运单id
					transportLinks:linkNode,//扫描环节
					driverName:that.$store.state.userInfo.userName,//司机名称
				}

				console.log(postData)

				that.httpRequest( "updateDeliveryScanNumber.do",postData,function(res){
					console.log(res)

					that.$alert('提交订单成功', '提示', {
			            confirmButtonText: '确定',
			            callback: action => {
				            that.$parent.loadData();
			            }
		           	})
				},"isLoading_")
	          
	        }).catch(() => {
	          // this.$message({
	          //   type: 'info',
	          //   message: '已取消删除'
	          // });          
	        });
		},
		// 跳转到 中转扫描 页面
		toTransitScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"TransitScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		// 跳转到 派件扫描 页面
		toDeliveryOrderPickScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"DeliveryOrderPickScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		// 跳转到 异常上报 页面
		toAbnormal (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"Abnormal",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryNoSF:that._orderInfo[index].clientOrderNo
				}
			})
		},	
		// 跳转到 签收扫描 页面
		toReceiptScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"ReceiptScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		// 跳转到 签单上传 页面
		toMassTransfer(index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"MassTransfer",
				query:{
					omsNo : that._orderInfo[index].omsNo
				}
			})

		},
    	// 跳转到 选择地址页面
    	toSelectAdd(BtnToUse){

    		event.stopPropagation();

    		var that = this;

    		if(BtnToUse == "ArriveReceivingPoint"){

				that.shipmentID = that.$store.state.Waybill.WaybillInfo.clickShipmentId;

				var postData = {
					shipmentId: that.shipmentID,//配载单id
			    };

				that.httpRequest("queryDeliverReceiveAddrInfo.do",postData,function(res){

					if(res.data.length == 1){

						console.log(res.data[0].receivePartyAddr1)

						that.$router.push({
							name:"ArrivalConfirm",
							query:{
								receiveAddr:res.data[0].receivePartyAddr1,//收货地址
							}
						})
					}else{
						that.$router.push({
							name:"SelectAddress",
							query:{
								BtnToUse:BtnToUse
							}
						})
					}
				},'isLoading_')

    		}else{
    			// 按地址交付

    			console.log(this.confirmNumOfDriverReceive)

    			if(this.confirmNumOfDriverReceive == 'F'){

    				that.$alert("请先确认收货数量再进行交付动作", '提示', {
				        confirmButtonText: '确定'
				   	})
				   	return;
    			}
    			this.$router.push({
					name:"SelectAddress",
					query:{
						BtnToUse:BtnToUse
					}
				})
    		}
    	},
    	//跳转到 送达页面
    	// arriveDestination(){

    	// },
		// 跳转到 订单交付页面
		toOrderDeliver(index){

			event.stopPropagation();

			var that = this;

			if(this.confirmNumOfDriverReceive == 'F' && that.shipStatus != '未交付'){

				that.$alert("请先确认收货数量再进行交付动作", '提示', {
			        confirmButtonText: '确定'
			   	})
			   	return;
			}

			var deliverNo_list = [];

			var validateArray = [];//验证数组

			// 交付
			if(index != "selectAll"){

				deliverNo_list[0] = that._orderInfo[index].deliveryId;

				that.$router.push({
					name:"orderDeliver",
					query:{
						deliverNo_list:deliverNo_list,
						selectAddr:that.selectAddr
					}
				})

				return;

			// 批量交付
			}else{

				for(var i = 0 ; i < that._orderInfo.length;i++){

					if($(".goodsInfo").eq(i).children(".goodsHead").children("span.iconfont").hasClass("icon-xuanzhong")){

						deliverNo_list[deliverNo_list.length] = that._orderInfo[i].deliveryId;

						validateArray[validateArray.length] = that._orderInfo[i].receivePartyContactName;
					}
				}

				if(!deliverNo_list.length){

					that.$alert("没有选中任何订单", '提示', {
				        confirmButtonText: '确定'
				   	})

				}else{

					that.$router.push({
						name:"orderDeliver",
						query:{
							deliverNo_list:deliverNo_list,
							selectAddr:that.selectAddr
						}
					})
				}
			}
		},
		//点击勾选订单
		selectData:function(idx,ifDeliver){

			var that = this;

			if(!ifDeliver){
				return;
			}

			var iconElement = $(".goodsInfo").eq(idx).children(".goodsHead").children("span.iconfont");
			
			if(iconElement.hasClass("icon-xuanzhong")){

				iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong")

			}else if(iconElement.hasClass("icon-weixuanzhong")){

				iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
			}

			var if_Element;
			var if_changeBtn = {
				Selected:false,
				unSelected:false
			};
			for(var n = 0; n < that._orderInfo.length; n++){

				if_Element = $(".goodsInfo").eq(n).children(".goodsHead").children("span.iconfont");

				if(if_Element.hasClass("icon-xuanzhong")){

					if_changeBtn.Selected = true;

				}else if(if_Element.hasClass("icon-weixuanzhong")){

					if_changeBtn.unSelected = true;

				}
			}

			if(if_changeBtn.Selected != if_changeBtn.unSelected){

				if($(".selectAll").html() == "取消全选"){

					$(".selectAll").html("全选");

				}else{
					$(".selectAll").html("取消全选");
				}
			}
		},
		// 点击全选按钮
		selectAll(){

			console.log(this._orderInfo)
			// console.log(1)
			// console.log(this._orderInfo[0])
			var that = this;

			if($(".selectAll").html() == "取消全选"){

				$(".selectAll").html("全选");

				var iconElement;

				for(var j = 0; j < that._orderInfo.length; j++){

					iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

					iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong");
				}

			}else{

				$(".selectAll").html("取消全选");

				var iconElement;

				for(var j = 0; j < that._orderInfo.length; j++){

					iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

					iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
				}
			}
		},
		// 明细展开、收起
		detail_takaUp(index){
			event.stopPropagation();

			var that = this;

			var this_span = $(".goodsInfo").eq(index).find(".TakeUp").find("span");

			var this_icon = $(".goodsInfo").eq(index).find(".TakeUp").find("i");

			if(this_span.html() == "查看货物信息"){

				$(".goodsInfo").eq(index).find(".goodsContent").show();

				this_span.html("收起明细"); 

				this_icon.css("transform","rotate(180deg)");

			}else{

				$(".goodsInfo").eq(index).find(".goodsContent").hide();

				this_span.html("查看货物信息");

				this_icon.css("transform","rotate(0deg)");
			}
		},
		// 跳转到二维码页面
		_ewm(omsNo){

			this.$router.push({
				name:"QRCode",
				query:{
					omsNo:omsNo
				}
			})
		},
		onCopy (){

			var that = this;
			that.ifTips = true;
			that.tips_Msg = "复制成功";

			setTimeout(function(){

				that.ifTips = false;
			},1000)
		},
    }
}
</script>

<style lang="less" scoped>
@import "../../../components/publicLess.less";
.orderHead{
	text-align: center;
	height: 65/50rem;
	line-height: 65/50rem;
	color: #000;
	background-color: #EDEFF3;
	position:relative;
	span{
		height: 65/50rem;
