  			&.ordernum{
  				i{
  					color: @PublicColor;
  					font-size: 37/50rem;
  					margin-left: 20/50rem;
  				}
  			}
  			span{
  				float: left;
  				height: auto;

		    	&:nth-child(2){
					width: 500/50rem;
		    	}
  			}
  			&>span:nth-child(1){
  				color:#000;
  			}
  			&>div{
				float: left;
				width: 50%;
				span{
	  				float: static;
	  				height: auto;

			    	&:nth-child(2){
						width:auto;
			    	}
	  			}
	  			&>span:nth-child(1){
	  				color:#000;
	  			}
  			}
  		}
  		.KeyInfoGroup{
			position: absolute;
			top: 20/50rem;
			right: 0;
			display: flex;
			&>div{
				width: auto;
				padding:0/50rem 10/50rem;
				border-radius:10/50rem;
				color: @PublicColor;
				border:2/50rem solid @PublicColor;
				font-weight: 700;
				margin-right: 20/50rem;
				&:last-child{
					margin-right: 0;
				}
			}
  		}
  		// &>div:not(:last-child){
  		// 	span:nth-child(1){
  		// 		width: 170/50rem;
  		// 	}
  		// }
  		&>span.icon-xuanzhong{
  			position:absolute;
  			top: 20/50rem;
  			right: 20/50rem;
			color:@PublicColor;
			font-size: 40/50rem;
  		}
  		&>span.icon-dayin{
  			position:absolute;
  			top: 20/50rem;
  			right: 10/50rem;
  			color: @PublicColor;
  			font-size: 36/50rem;
  			span{
  				font-size: 28/50rem;
  				margin-left: 10/50rem;
  			}
  		}
  	}
  	.goodsBtn{
  		overflow: hidden;
  		.TakeUp{
  			float: left;
	  		text-align: center;
	  		height: 60/50rem;
	  		line-height: 60/50rem;
	  		overflow: hidden;
	  		div{
	  			color: @PublicColor;
	  			overflow: hidden;
	  			display: inline-block;
	  			height: 80/50rem;
	  			margin: 0 auto;
	  		}
	  		span{
	  			float: left;
	  		}
	  		i{
	  			float: left;
	  			font-size: 50/50rem;
	  		}
	  	}
		span{
			float: right;
			height:60/50rem;
			line-height: 60/50rem;
			width: 200/50rem;
		}
		span.ewm{
			margin-right: 15/50rem;
		}
  	}
  	._btn{
  		margin-top:15/50rem; 
  		overflow: hidden;
  		display: flex;
		justify-content : flex-end;
		span{
	  		margin-bottom:15/50rem; 
			height:60/50rem;
			line-height: 60/50rem;
			// max-width: 35%;
			width: 25%;
			margin-left: 10/50rem;
		}
  	}
}

//按钮 此页面公共样式
span.g_btn{
	border-radius:10/50rem;
	color:#fff;
	background-color: #6897FF;
	text-align: center;
}
span.g_btn_disab{
	background-color:#aaa;
}
.bottomBtn{
	width: 100%;
	height: 90/50rem;
	position: fixed;
	display: flex;
	bottom: 0;
	div{
		flex-grow: 1;
		border-radius: 35/50rem;
		margin-right: 15/50rem;
		margin-left: 15/50rem;
	}
}
.BatchCommitPopBox{
	.BatchCommit_ChangeInfo{
		&>div{
