						font-size: 30/50rem;
					}
				}
				&:nth-child(3){
					width: 80/50rem;
					text-align: center;
					font-size: 27/50rem;
					flex-shrink: 0;
				}
			}
		}
	}
}
.commit-button{
	height: 70/50rem;
	line-height: 70/50rem;
	font-size: 30/50rem;
	margin-top: 30/50rem;
	margin-bottom: 10/50rem;
	background-color: #038EEA;
	border-radius: 35/50rem;
	flex: 1;
	text-align: center;
	color: #fff;
}
.BatchCommit{
	margin-right: 10/50rem;
}
</style>


