<!-- _template_WaybillInfo.vue   配载单信息 组件-->
<template>
  <div class="_template_WaybillInfo">
	<div  class="bigList" v-for='(dataItem,index) in shipmentListData'  :id="index"  :key='index'  >
	  <div  class="listHead"  @click="Details_waybill(dataItem.shipmentID,index,LinkStatus)" >
		<!-- 导航 -->
		<span   @click="Navigation(index)" class="Navigation">导航</span>
		<!-- 状态 -->
		<span v-if='dataItem.status' class="LicensePlate">{{dataItem.status}}</span>
		<!-- 起终点城市 -->
		<div  class="firstLine">
		  <!-- <span>{{dataItem.startCity}}</span> -->
		  <i class="iconfont" ></i>
		  <!--  icon-jiang -->
		  <!-- <span>{{dataItem.endCity}}</span> -->
		</div>
		<!-- 单号 -->
		<div class="codestyle">
		  <span>配载单号：</span>
		  <span @click='stopevent' v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError">{{dataItem.shipmentCode}}</span>&nbsp;&nbsp;
		  <i  @click='stopevent'  v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>&nbsp;&nbsp;
		  <i @click="_ewm(dataItem.shipmentCode)" class="iconfont icon-iconfonterweima" ></i>
		  <!-- <span class="BackBottleClass" v-if="dataItem.backBottleFlag == 'T'">回瓶单</span> -->
		</div>
		<div>
		  <span>货&nbsp;&nbsp;&nbsp;量：</span>
		  <span>{{dataItem.countDelivery}} 单 &nbsp; </span>
		  <span>{{(dataItem.vol * 0.000001).toFixed(1)}} 方 &nbsp; </span>
		  <span>{{(dataItem.wt * 0.000001).toFixed(1)}} 吨 &nbsp; </span>
		</div>
		<div>
		  <div>
			<span>物流公司：</span>
			<span>{{dataItem.transPartyName}}</span>
		  </div>
		</div>
		<!-- <div><span>收 货 &nbsp;人：</span><span>{{dataItem.receivePartyName}}</span></div> -->
		<div v-if="dataItem.issueAddrLiat.length==1">
		  <span>装货地址：</span>
		  <span>{{dataItem.issueAddrLiat[0].issueAddr1}}</span>
		</div>
		<div v-if="dataItem.issueAddrLiat.length==1">
		  <span>联 系 &nbsp;人：</span>
		  <span>{{dataItem.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{dataItem.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i v-if="dataItem.issueAddrLiat[0].issuePhone" @click="callPhone(dataItem.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
		</div>
		<div  v-if="dataItem.issueAddrLiat.length !=1 &&dataItem.issueAddrLiat.length !=0">
		  <i class="iconfont icon-tishi" ></i>
		  <span  @click="MultiAddress(index)" style="color:#038EEA; ">查看多装货地址</span>
		</div>
	<!-- 	<div v-if="dataItem.issueAddrLiat.length == 1 && dataItem.issueAddrLiat[0].phoneAppointTime">
		  <span>预约时间：</span>
		  <span>{{dataItem.issueAddrLiat[0].phoneAppointTime}}</span>
		</div>
		<div v-if="dataItem.issueAddrLiat.length == 1 && dataItem.issueAddrLiat[0].phoneAppointTime">
		  <span>预约状态：</span>
		  <span>{{dataItem.issueAddrLiat[0].appointLoadingStatus == "NEW" ? "新预约": dataItem.issueAddrLiat[0].appointLoadingStatus == "YI" ? "预约成功" : dataItem.issueAddrLiat[0].appointLoadingStatus == "REFUSE" ? "拒绝预约" : ""}}</span>
		</div> -->
		<!-- // appointLoadingStatus;//装车预约状态：NEW=新预约，YI=已确定，REFUSE=已拒绝 -->
	  </div>
	  <p>
		<!-- 提货扫描 -->
		<!-- <span v-if="LinkStatus == 'Link_PickUp'"  @click="toDeliveryScanXH(index)">提货扫描</span> -->

		<!-- 提交配载  提货、中转-->
		<span v-if="LinkStatus == 'Link_PickUp' || LinkStatus == 'Link_Transit'"  @click="SubmitShipment_PickUp(index,'配载')">提交配载</span>

		<!-- 装车扫描  中转-->
		<!-- <span v-if="LinkStatus == 'Link_Transit'"  @click="toTransitScan(index)">装车扫描</span> -->
		
		<!-- 提交装车  派件-->
		<span v-if="LinkStatus == 'Link_ToSend' && dataItem.loadingStatus == 'WEI'"  @click="SubmitShipment_PickUp(index,'装车')">批量提交装车</span>

		<!-- 提交签收  签收-->
		<!-- <span v-if="LinkStatus == 'Link_ToSend' && dataItem.loadingStatus == 'YI'"  @click="SubmitShipment_PickUp(index,'签收')">提交签收</span> -->

		<!-- 签收扫描 -->
		<!-- <span v-if="LinkStatus == 'Link_ToSend'"  @click="toReceiptScan(index)">签收扫描</span> -->

		<!-- 批量交接 -->
		<!-- <span v-if="LinkStatus == 'Link_PickUp' || LinkStatus == 'Link_Transit'"  @click="toMassTransfer(index)">批量交接</span> -->

		<!-- 异常上报 中转-->
		<span v-if="LinkStatus == 'Link_Transit'"  @click="toAbnormal(index)">异常上报</span>

		<!-- 途中轨迹 -->
		<!-- <span  @click="toTrackOnTheWay(index)">途中轨迹</span> -->

	  </p>
	  <!-- <p>
	  	批量交接
	  		<span  v-if="LinkStatus == 'Link_ToSend'"  @click="toMassTransfer(index)">派件批量交接</span>
	  		<span  v-if="LinkStatus == 'Link_ToSend'"  @click="toMassTransfer(index)">签收批量交接</span>
	  </p> -->
	</div>
	<!-- 查看多装货地址弹出框 -->
	<div class="MultiAddress">
	  <div>
		<i @click="closeAddress"  class="iconfont icon-iconfontcuowu" ></i>
		<div class="addressHead">
		  <span  @click="address_SwitchView(idx)"  v-for='(itemAddHead,idx) in address_listData'  :id="idx"  :key='idx' >地址{{idx+1}}</span>
		</div>
		<div class="addressContent">
		  <div>
		  	<span>地址：</span>
		  	<span>{{currentAddressInfo.issueAddr1}}</span>
		  </div>
		  <div>
		  	<span>联系人：</span>
		  	<span>{{currentAddressInfo.issueUser}}</span>
		  </div>
		  <div>
		  	<span>电话：</span>
 		  	<span>{{currentAddressInfo.issuePhone}}</span> &nbsp;&nbsp;&nbsp;&nbsp;  <i v-if="currentAddressInfo.issuePhone" style="color:#038EEA; " @click="callPhone(currentAddressInfo.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
		  </div>
		  <div v-if="currentAddressInfo.phoneAppointTime">
		  	<span>预约时间：</span>
		  	<span>{{currentAddressInfo.phoneAppointTime}}</span>
		  </div>
		  <div v-if="currentAddressInfo.phoneAppointTime">
		  	<span>预约状态：</span>
		  	<span>{{currentAddressInfo.appointLoadingStatus}}</span>
		  </div>
		</div>
	  </div>
	</div>
	<!-- 导航地址弹出框 -->
	<div class="Navigate_MultiAddress" v-if="typeMShow">
	  <div>
		<div class="SelectTitle">请选择导航地址</div>
		<div class="SelectContent">
		  <div class="addressContent" @click="Navigate_SelectComit(itemAddress.issueAddr1)"  v-for='(itemAddress,idx) in Navigate_addressList'  :id="idx"  :key='idx'>
			<div>
			  <span>{{itemAddress.issueAddr1}}</span>
			</div>
			<div>
			  <span>{{itemAddress.issueUser}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{itemAddress.issuePhone}}</span><i style="color:#038EEA;" @click="callPhone(itemAddress.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
			</div>
		  </div>
		</div>
		<div class="cancel"   @click="selectCancel('e')">取消</div>
	  </div>
	</div>
	<div v-if="ifTips" class="msg_tips msg_tips1">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<span>{{tips_Msg}}</span>
		</div>
	</div>
  </div>
</template>

<script>
import $ from "jquery"

export default {
  name:"WaybillInfo_template",
  data(){
    return{
      Exceed:false,//预约时间是否超出当前时间
      currentAddressInfo:{},//当前显示的地址数组信息
      address_listData:[],//当前多地址配载单的地址数组信息 shipmentListData[i].issueAddrLiat
      typeMShow:false,//导航地址弹出框是否出现
      Navigate_addressList:[],//导航地址列表
      DriverIssueShow:false,//司机确认 发货数量弹出框 是否显示
      DriverReceiveShow:false,//司机确认 收货数量弹出框 是否显示
      CurrentLocation:'',//当前位置
	  shipmentIdx:0,//当前点击的配载单idx
	  confirmNumOfDriverIssue:'',//发货数量确认状态
	  confirmNumOfDriverReceive:'',//收货数量确认状态
	  tips_Msg:"",//提示信息
	  ifTips:false
    }
  },
  props:{
	shipmentListData:{
	},
	LinkStatus:'',//当前环节
  },
  created(){
    var that = this;
  },
  methods:{
	SetCurrAddress:function(address) {

		if(address == "") {

			this.$alert('定位失败', '提示', {
        		confirmButtonText: '确定',
        		callback: action => {
        		}
   			})
		}else {

			this.CurrentLocation = address;
		}
	},
	// 跳转到二维码页面
	_ewm(shipmentCode){
		event.stopPropagation();
		var that = this;

		this.$route.query.LinkStatus = this.LinkStatus;

		console.log(this.$route.query.LinkStatus)

		this.$router.push({
			name:"QRCode",
			query:{
				shipmentCode:shipmentCode,//配载单号
				LinkStatus:that.LinkStatus,//当前环节
			}
		})
	},
	// 跳转到 装货预约 页面
	// LoadingAppoint  (index){
	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"LoadingAppoint",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	// 跳转到 到达确认 页面
	// toArrivalConfirm  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"ArrivalConfirm",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	// 跳转到 途中反馈 页面
	// toWayFeedback  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"WayFeedback",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	// 跳转到 提货扫描 页面
	toDeliveryScanXH (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"DeliveryScanXH",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	// 提交配载  提货
	SubmitShipment_PickUp(index,text){
		event.stopPropagation();
		
		var that = this;

		this.$confirm("确认要提交"+text+"吗？ 配载单号："+ that.shipmentListData[index].shipmentCode , '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        	var linkNode = ''; // 需要传参的linkNode类别

	        if(that.LinkStatus == 'Link_PickUp'){
	        	linkNode = '10';
	        }
	        if(that.LinkStatus == 'Link_Transit'){
	        	linkNode = '20';
	        }
	        if(that.LinkStatus == 'Link_ToSend' && that.shipmentListData[index].loadingStatus == 'WEI'){
	        	linkNode = '30';
	        }
	        if(that.LinkStatus == 'Link_ToSend' && that.shipmentListData[index].loadingStatus == 'YI'){
	        	linkNode = '40';
	        }

        	var postData = {
				shipmentCode:that.shipmentListData[index].shipmentCode,//配载单号
				transportLinks:linkNode,//扫描环节
				driverName:that.$store.state.userInfo.userName,//司机名称
			}

			console.log(postData)

			that.httpRequest( "updateDeliveryScanNumber.do",postData,function(res){
				console.log(res)


	          that.$alert('提交成功!', '提示', {
	            confirmButtonText: '确定',
	            callback(){
	              that.$parent.loadData();
	            }
	          })
			},"isLoading_")
          
        }).catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '已取消删除'
          // });          
        });

		// that.$alert("确认要提交配载吗？ 配载单号："+ that.shipmentListData[index].shipmentCode , '提示', {
  //           confirmButtonText: '确定',

  //           callback: action => {
	            
  //           }
  //      	})

	},
	// 跳转到 中转扫描 页面
	toTransitScan (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"TransitScan",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	// 跳转到 批量交接 页面
	toMassTransfer (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"MassTransfer",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	// 跳转到 异常上报 页面
	toAbnormal (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"Abnormal",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	// 跳转到 途中轨迹 页面
	toTrackOnTheWay (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"TrackOnTheWay",
			query:{
				shipmentID : that.shipmentListData[index].shipmentID
			}
		})
	},
	// // 跳转到 发车确认 页面
	// toDepartureConfirm  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};
		
	// 	var that = this;

	// 	this.$router.push({
	// 		name:"DepartureConfirm",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	// 跳转到配载单详情
	Details_waybill(shipmentID,index){

		var that = this;
		if(this.$route.name == 'WaybillInfo'){
			return;
		}
		if(this.DriverError(index) == 10){
			return
		};

		this.$store.state.Waybill.WaybillInfo.clickWaybillInfo = [this.shipmentListData[index]];

		this.$store.state.Waybill.WaybillInfo.clickShipmentId = shipmentID;

		this.$router.push({
            name:"WaybillInfo",
            query:{
            	LinkStatus:that.LinkStatus
            }
        })
	},
	// 当回瓶单 回瓶单信息有误时， 该单不可操作	
	// 当配载单里没有运单时  不可操作  
	DriverError(index){

		var that = this;

		var thisShipment = that.shipmentListData[index];

		// 配载单里没有运单
		if(thisShipment.countDelivery == 'F'){

			that.$alert('该配载单中没有运单，请与调度联系', '提示', {
	            confirmButtonText: '确定',
	            callback: action => {
        
		           	if(that.$route.name == 'Waybill'){

	            		that.$emit('fatherMethodloadData');

	            	}else{
	            		that.$router.push({
				            name:"WaybillInfo",
				        })
	            	}
	            }
           	})
           	return 10;
		}
	},
	onCopy (){

		var that = this;
		that.ifTips = true;
		that.tips_Msg = "复制成功";

		setTimeout(function(){

			that.ifTips = false;
		},1000)
	},
  }
}
</script>

<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .bigList{
  	background-color:#fff;
	border-radius: 20/50rem;
	text-align: left;
	margin-bottom: 15/50rem;
	overflow: hidden;
	&>p{
		margin:20/50rem 20/50rem;
		display: flex;
		justify-content :  flex-end;
		span{
			flex-grow:1;
			max-width: 32%;
			margin-left: 15/50rem;
			text-align: center;
			background-color: @PublicColor;
			color: #fff;
			border-radius: 15/50rem;
			padding: 8/50rem 0;
		}
		span:nth-child(1){
			margin-left:0;
		}
	}
  }
  .listHead{
	overflow: hidden;
	position: relative;
	width: 100%;
	padding: 20/50rem;
	padding-bottom: 0;
	// background-color: #F4F4F4;
	box-sizing: border-box;
	&>div{
		line-height: 55/50rem;
		overflow: hidden;
		&>div{
			float: left;
		}
		&.firstLine{
			width: 600/50rem;
			span{
				float: left;
				&:nth-child(1){
					margin-left: 180/50rem;
				}
			}
			.icon-jiang{
				margin:0 15/50rem;
				float: left;
			}
		}
		&.codestyle{
			overflow: hidden;
			span{
				float: left;
			}
			i{
				float: left;
				margin: 0 15/50rem;
			}
		}
		&.mileage{
			&>div{
				width: 355/50rem;
			}
		}
	}
	.iconfont{
		font-size: 35/50rem;
		color: @PublicColor;
		margin-right: 5/50rem;
	}
	.LicensePlate{
		position: absolute;
		top: 20/50rem;
		left: 0;
		background-color: @PublicColor;
		padding:0  25/50rem;
		height: 50/50rem;
		line-height: 50/50rem;
		color: #fff;
		border-radius: 0 25/50rem 25/50rem 0;
	}
	.Navigation{
		position: absolute;
		right: 20/50rem;
		top: 20/50rem;
		width:100/50rem;
		height: 100/50rem;
		line-height: 100/50rem;
		background-color: @PublicColor;
		border-radius: 50%;
		text-align: center;
		color: #fff;
	}
	.BackBottleClass{
		width: 120/50rem;
		text-align: center;
		color: #fff;
		margin-top: 5/50rem;
		height: 45/50rem;
		line-height: 45/50rem;
		background-color:#00C800;
		border-radius:25/50rem ;
	}
  }
  .confirmNumOfDriverIssue,.confirmNumOfDriverReceive{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left:0;
    background-color: rgba(0,0,0,0.5);
    z-index: 1000;
    text-align: left;
    ._DriverIssue_container{
	    width: 650/50rem;
	    padding:0 20/50rem 20/50rem;
	    box-sizing:border-box;
	    background-color: #fff;
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    font-size: 32/50rem;
	    margin-top: -403/50rem;
	    margin-left: -325/50rem;
	    border-radius: 10/50rem;
	    &>.icon-iconfontcuowu{
	        position: absolute;
	        top: -10/50rem;
	        right: -10/50rem;
	        font-size: 55/50rem;
	    }
	    &>._DriverIssue_head{
	    	height: 90/50rem;
	    	line-height: 90/50rem;
	    	font-weight: 600;
	    	text-align: center;
	    	font-size: 37/50rem;
	    }
	    &>._DriverIssue_btn{
	    	width: 100%;
	    	box-sizing:border-box;
	    	display: flex;
	    	margin-top: 20/50rem;
	    	span{
	    		flex-grow: 1;
	    		height: 70/50rem;
	    		line-height: 70/50rem;
	    		background-color:@PublicColor;
	    		color: #fff;
	    		border-radius:8/50rem;
	    		text-align: center;
	    		margin-right: 30/50rem;
	    	}
	    	span:last-child{
	    		margin-right: 0;
	    	}
	    }
	}
  }
  // 发货
  .confirmNumOfDriverIssue ._DriverIssue_container ._DriverIssue_content{
	div._content_DriverIssue_code{
			line-height: 50/50rem;
	}
	div._content_DriverIssue_List{
		overflow: scroll;
		max-height: 600/50rem;
		&>div:first-child{
			line-height: 65/50rem;
		}
	}
	div.DriverIssue_detaiList{
		color: #777;
		margin-bottom: 15/50rem;
		margin-top: 5/50rem;
		div{
			line-height: 45/50rem;
		}
	}
  }
  // 收货
  .confirmNumOfDriverReceive ._DriverIssue_container ._DriverIssue_content{
	div._content_DriverIssue_code{
		line-height: 50/50rem;
	}
	div._content_DriverIssue_List{
		overflow: scroll;
		max-height: 600/50rem;
		color: #777;
		div.DriverIssue_detaiList{
			margin-top: 5/50rem;
			margin-bottom: 15/50rem;
			&>div:nth-child(1){
				color: @PublicColor;
			}
		}
		div.DriverReceivedamage{
			color: #F01418;
		}
	}
  }
</style>



