<template>
	<div  class="Login">
		<!-- <header><span>登录</span></header> -->
		<div class="container">
		  <div class="LoginLogo">
		  	<img src="../../assets/images/logo.png" alt="">
		  </div>
		  <!--loginContainer-->
			<div class=" identeContainer">
				<!-- <div>
					<i class="iconfont icon-gerenzhongxin"></i>
					<input class='userName'  ref="userName"    @keyup.enter="getFocus('password')"  v-model="userName"  placeholder='请输入手机号' ></input>
					<i v-if="userName" @click="clearInput('userName')" class="iconfont icon-iconfontcuowu"></i>
				</div>
				<div>
				 	<i class="iconfont icon-mima" ></i>
				 	<input class='pswd' type='password'  ref="password"   v-model="password"  @keyup.enter="login"  placeholder='请输入密码' ></input>
				 	<i v-if="password" @click="clearInput('password')" class="iconfont icon-iconfontcuowu"></i>
				</div> -->
				<div>
				  	<input placeholder='请输入手机号'   ref="userName"   @keyup.enter="getFocus('password')"   v-model="userName" ></input>
				  	<!-- <i v-if="userName" @click="clearInput('userName')" class="iconfont icon-iconfontcuowu"></i> -->
				  	
			    </div>
			    <div>
			    	<input placeholder='请输入验证码'   ref="password"   @keyup.enter="sfkcLogin"   v-model="password" ></input>
			    	<!-- <i v-if="password" @click="clearInput('password')" class="iconfont icon-iconfontcuowu"></i> -->
			    	<div class="button" @click='sendIdentifying'>获取验证码</div>
			    </div>
					<!-- sfkcLogin -->
			  <button class='login' @click='login'>登录</button>
			  <!-- <div class='repswd'><span @click='repwd'>忘记密码</span><span @click='reg'>注册</span></div> -->
			</div>
		</div>
		<!-- <div class="verseion">{{this.$store.state.App_Version}}</div> -->
	</div>
</template>
<script type="text/javascript">
	import Axios from "axios"
	import $ from "jquery"
	require('../../assets/js/plugins.js');
	require('../../assets/js/uu.js');
	export default {
		name:"Login",
		data(){
			return{
				userName:"",//用户名
				password:"",//密码
				WarehousesList:[],//仓库列表
				selectMShow:false,//选择仓库蒙版是否出现
				content:[],
			    identifyingCode:'',//验证码
			    tips_Msg:"",//提示信息
				ifTips:false,//提示信息是否显示
				MinuteNum:60,//发送验证码倒计时
				CountDown:'',//
			}
		},
		mounted() {

			var that =this;
			// this.$refs['userName'].focus();
			$("input").attr("maxlength","50");

			// 跳转到微信绑定账号页面
			// window.WXBind_NO_Ajax = this.WXBind_NO_Ajax;

			// 微信登录后 记住账号密码 跳转到首页
			// window.WXBind_YES_Ajax = this.WXBind_YES_Ajax;

			//----------- 获取版本号--------需要重新写
			// window.VersionShow = this.VersionShow;
			
			// 监听plusready事件  
			document.addEventListener( "plusready", function(){
				// android获取本地存储用户名密码
				that.pluginSetUser();

			}, false );

			// ios检查是否安装微信 未安装则移除图标
			// window.WXInstall_Check_Ajax = this.WXInstall_Check_Ajax;

			// ----------- android设备还是ios设备--------需要重新写
			window.Device_Ajax = this.Device_Ajax;
		},
		created(){

			this.getCookie("e");

		},
		// 页面销毁前
		beforeDestroy(){

			var that = this;

			clearInterval(that.CountDown)
		},
		methods:{
			// 保存账号密码
			pluginGetUser(u,p) {
				try{
					plus.plugintest.PluginGetUserFunction(u,p, function( result ) {
						
						return;
					},function(result){
						return;
					});
				}
				catch(error) {
				}
				
        	},
        	// 获取账号密码
        	pluginSetUser() {
        		var that = this;
				try{
					plus.plugintest.PluginSetUserFunction(function( result ) {

						that.userName = result[0];
						// that.password = result[1];
				
						return;
					},function(result){
						return;
					});
				}
				catch(error) {
				}
				
        	},
        	// 点击 发送验证码 按钮
			sendIdentifying (){

			    var that = this;

			    if(that.MinuteNum != 60){
			    	return;
			    }

			    if (!that.userName.length) {

			    	that.$alert("请输入手机号", '提示', {
				        confirmButtonText: '确定',
				        callback: action => {
				            
				        }
				   	})

			    }else if(that.userName.length != 11){

			    	that.$alert("请输入正确的手机格式", '提示', {
				        confirmButtonText: '确定'
				   	})

			    }else{

			      var params = {
			        cellphone: that.userName,
			        next_page: 'denglu'
			      }

			      this.httpRequest( "sendYzmToCellphone.do",params,function(res){

			      	that.ifTips = true;
					that.tips_Msg = "验证码已发送";

					$(".button").css("color","#555").html("重新发送(" + that.MinuteNum + ")")

					that.CountDown = setInterval(function(){

						that.MinuteNum -- ;

						$(".button").html("重新发送(" + that.MinuteNum + ")")

						if(that.MinuteNum  < 0 ){

							$(".button").css("color"," #038EEA").html("重新获取验证码");

							that.MinuteNum = 60;

							clearInterval(that.CountDown)
						}
						
					},1000)

					setTimeout(function(){

						that.ifTips = false;
					},1500)

			      })
			    }
			},
			// 检查手机是否安装微信，以免App Store审核被拒
			WXInstall_Check_Ajax:function(isInstall) {

				if(isInstall == "NO") {

					this.$refs['ThirdPartyLogin'].remove();
				}
			},
			loginVueMounted:function(){

				// 安卓
				try {

					CallAndroidOrIOS.callAndroid("登录页面已加载", "");

				} 
				catch(error) {

					// console.log("没有CallAndroidOrIOS.callAndroid方法")
				}

				// 苹果
				try {

						CallAndroidOrIOS("登录页面已加载");
				}
				catch(error) {

					// console.log("没有CallAndroidOrIOS方法")
				}
			},
			KeepUserNameAndPassord:function(u, p){

				// 安卓
				try {

					CallAndroidOrIOS.callAndroid("记住帐号密码", u, p);

				} 
				catch(error) {

					console.log("没有CallAndroidOrIOS.callAndroid方法")
				}

				// 苹果
				try {

					CallAndroidOrIOS("记住帐号密码", u, p);
				}
				catch(error) {
					
					console.log("没有CallAndroidOrIOS方法")
				}
			},
			// 跳转到微信绑定账号页面
			WXBind_NO_Ajax:function(openid) {

				this.$store.state.WX_Openid = openid;
				this.$router.push({
					name:"Binding",
					query:{
					}
				})
			},
			// 微信登录后 记住账号密码 跳转到首页
			WXBind_YES_Ajax:function(data) {

				// 填充帐号
				var str = decodeURIComponent(data);
				var obj = JSON.parse(str);
				this.userName = obj.data.cellphone;

				this.$store.state.userInfo = obj.data;

				this.$store.state.NoSearchFrom = true; // 不是从搜索页面到我的任务页面

				this.setCookie(this.userName,this.password,10*365*60);

				this.pluginGetUser(that.userName, that.password);
				// this.KeepUserNameAndPassord(this.userName, this.password);

				this.$router.push({
					name:"Index",
					query:{
					}
				})
			},
			// 显示版本号
			VersionShow:function(v) {

				this.$store.state.App_Version = v;
			},
			Device_Ajax:function(d) {

				this.$store.state.Device = d;
			},
			// 登录
			sfkcLogin(){
				
				var that = this;
				
				if(!this.userName){

					that.$alert("请输入手机号", '提示', {
				        confirmButtonText: '确定',
				        callback: action => {
				            
				        }
				   	})
					
					return;

				}else if(!this.password){

					that.$alert("请输入验证码", '提示', {
				        confirmButtonText: '确定',
				        callback: action => {
				            
				        }
				   	})
					
					return;
				}

				var postData = {
					"cellphone":this.userName,//手机号
					"verificationCode":this.password,//验证码
				}

				this.httpRequest( "sfkcAppLogin.do",postData,function(res){

					that.$store.state.userInfo = res.data;
					that.$store.state.WaybillData = [];//我的任务页面数据清空
					that.$store.state.Waybill_NoSearchFrom = true; // 不是从搜索页面到我的任务页面
					that.$store.state.Waybill_needRefresh = true;//我的任务 页面数据是否需要刷新

					that.setCookie(that.userName,'',10*365*60);

					// that.KeepUserNameAndPassord(that.userName, that.password);

					that.pluginGetUser(that.userName, '');

					that.$router.push({
						name:"Index",
						query:{
						}
					})
				})
			},
			// 点击登录按钮
			login:function(){

				var that = this;
				
				if(!this.userName){

					that.$alert("用户名不能为空", '提示', {
				        confirmButtonText: '确定',
				        callback: action => {
				            
				        }
				   	})
					
					return;

				}else if(!this.password){

					that.$alert("密码不能为空", '提示', {
				        confirmButtonText: '确定',
				        callback: action => {
				            
				        }
				   	})
					
					return;
				}

				var postData = {
					"cellphone":this.userName,//手机号
					"password":this.password,//密码
					"wxOpenid":"",//openid
				}

				this.httpRequest( "login.do",postData,function(res){

					that.$store.state.userInfo = res.data;
					that.$store.state.WaybillData = [];//我的任务页面数据清空
					that.$store.state.Waybill_NoSearchFrom = true; // 不是从搜索页面到我的任务页面
					that.$store.state.Waybill_needRefresh = true;//我的任务 页面数据是否需要刷新

					that.$store.state.CostCount_StoreData = [];//费用统计页面数据清空
					that.$store.state.CostCount_NoSearchFrom = true; // 不是从搜索页面到费用查询页面
					that.$store.state.CostCount_needRefresh = true;//费用统计  页面数据是否需要刷新

					that.setCookie(that.userName,that.password,10*365*60);

					// that.KeepUserNameAndPassord(that.userName, that.password);

					that.pluginGetUser(that.userName, that.password);

					that.$router.push({
						name:"Index",
						query:{
						}
					})
				})
			},
			// 跳转到 忘记密码 页面 (验证手机号)
			"repwd":function(){

				this.$router.push({
					name:"Identifying",
					query:{
						page:"ForgotPwd"
					}
				})
			},
			// 跳转到 注册 页面 (验证手机号)
			"reg":function(){

				this.$router.push({
					name:"Identifying",
					query:{
						page:"Reg"
					}
				})
			},
			// 第三方登录
			ThirdPartyLogin:function(){

				// 安卓
				try {

					CallAndroidOrIOS.callAndroid("微信登录","");

				} 
				catch(error) {

					//console.log("没有CallAndroidOrIOS.callAndroid方法")
				}

				// 苹果
				try {

						CallAndroidOrIOS("微信登录");

				}
				catch(error) {

						//console.log("没有CallAndroidOrIOS方法")
				}
			},
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";

	.Login{
		overflow: hidden;	
	}

	.LoginLogo{
	    padding:80/50rem 0;
	    margin-bottom: 50/50rem;
	    text-align: center;
	    img{
	      width: 200/50rem;
	    }
	}
	.loginContainer{
		width:638/50rem;
		margin:60/50rem auto 0;
		&>div{
			width:638/50rem;
		    height:70/50rem;
		    position: relative;
		    margin-bottom: 50/50rem;
		    .icon-gerenzhongxin,.icon-mima{
		    	position: absolute;
		    	top:8/50rem;
		    	left:20/50rem;
		    	color: @PublicColor;
				font-size: 45/50rem;
				font-weight: 500;
		    }
		    .icon-mima{
		    	top: 12/50rem;
		    }
		    .icon-iconfontcuowu{
				right:20/50rem;
				top: 17/50rem;
			}
		}
	    .userName,.pswd{
		    width:638/50rem;
		    height:70/50rem;
		    border-radius: 10/50rem;
		    padding-left: 82/50rem;
		    font-size: 26/50rem;
		    box-sizing: border-box;
		}
		div.repswd{
		  margin-top: 30/50rem;
		  font-size: 30/50rem;
		  color: #777;
		}
		div.repswd>span:nth-child(1){
		  float: left;
		}
		div.repswd>span:nth-child(2){
		  float: right;
		}
	}
	.login{
	    width:100%;
	    height:73/50rem;
	    line-height: 73/50rem;
	    font-size: 30/50rem;
	    background-color:@PublicColor;
	    color: #fff;
	    margin-top: 50/50rem;
	    margin: 50/50rem auto;
	    border:none;
	    border-radius: 36.5/50rem;
	    outline: none;
	}
	.identeContainer{
		padding: 0 50/50rem;
		box-sizing: border-box;
		width:100%;
		height: 100%;
		font-size: 30/50rem;
		&>div{
			position: relative;
		    overflow: hidden;
		    margin-bottom: 70/50rem;
		    height: 65/50rem;
		    width: 100%;
		    border-bottom: 1/50rem solid #aaa;
			.icon-iconfontcuowu{
				right:20/50rem;
				color: #777;
				top: 12/50rem;
			}
			&>input{
				width: 620/50rem;
			}
			&:nth-child(2){
				.icon-iconfontcuowu{
					right:240/50rem;
				}
				&>input{
					width: 420/50rem;
				}
			}
			&>span:nth-child(1){
			  float: left;
			  margin-right: 30/50rem;
			  height:45/50rem;
			  line-height: 48/50rem;
			  display: block;
			}
			&>input{
			  float: left;
			  width:100%;
			  height: 60/50rem;
			  border: none;
			  border-radius: 0;
			}
			&>.button{
			  position: absolute;
			  right: 0;
			  top: 0;
			  height: 50/50rem;
			  line-height: 50/50rem;
			  box-sizing: border-box;
			  padding:0/50rem 15/50rem;
			  font-size: 28/50rem;
			  color: @PublicColor;
			}
		}
		.g_signBtn{
			border-radius:32.5/50rem;
		}
	}
	.ThirdPartyLogin{
		width: 100%;
		overflow: hidden;
		margin-top: 290/50rem;
		.ThirdParty_title{
			margin: 0 auto;
			height: 50/50rem;
			&>div{
				float: left;
				height: 50/50rem;
				font-size:30/50rem;
				color: @PublicColor;
			}
			&>div:nth-child(1),&>div:nth-child(3){
				height: 3/50rem;
				margin-top: 22.5/50rem;
				width:247.5/50rem;
			    background:-webkit-gradient(linear, left top, right top, from(#fff), to(@PublicColor));
			}
			&>div:nth-child(3){
				background:-webkit-gradient(linear, right top, left top, from(#fff), to(@PublicColor));
			}
			&>div:nth-child(2){
				width: 255/50rem;
				text-align: center;
			}
		}
		.ThirdParty_content{
			width: 80/50rem;
			height: 80/50rem;
			border-radius: 50%;
			border:1/50rem solid #469E3D;
			color:#469E3D;
			margin: 30/50rem auto 30/50rem;
			text-align: center;
			line-height: 73/50rem;
			i{
				font-size: 45/50rem;
			}
		}
	}
	@media screen and (max-width: 374px) {	
		.ThirdPartyLogin{
			.ThirdParty_content{
				border:3/50rem solid #469E3D;
			}
		}
	}

	.verseion{
		width:200/50rem;
		height: 40/50rem;
		color: #ccc;
		font-size: 29/50rem;
		text-align: center;
		position: fixed;
		bottom: 0;
		right: 0;
	}
</style>


