<template>
	<div  class="ScanQueryOrderInfo">
		<header><i class="iconfont icon-xiangzuo1"   @click="goprev"></i><span>定位异常单</span></header>
		<div class="container">
		    <div class="content-box">
		      <v-scan @scanData="scanData"  :loadingData="loadingData"></v-scan>
		    </div>
		    <div class="orderInfo">
		    	<div class="AbnormalItem" v-show='AbnormalInfo_show'>
					<div class="nomalList">
						<div><span>订单号：</span><span>SF234432432</span></div>
						<div><span>发起时间：</span><span>2020-3-12 08:34:23</span></div>
						<div>
							<div><span>发起人：</span><span>sf</span></div>
							<div><span>发起人承运商：</span><span>sf</span></div>
						</div>
						<div>
							<div><span>处理人：</span><span>小王</span></div>
							<div><span>处理人类型：</span><span>承运商</span></div>
						</div>
						<div>
							<div><span>处理公司：</span><span>某公司</span></div>
							<div><span>发运模式：</span><span>-</span></div>
						</div>
						<div>
							<div><span>异常环节：</span><span>派件</span></div>
							<div><span>异常类型：</span><span>操作类</span></div>
						</div>
						<div><span>异常原因：</span><span>少件</span></div>
						<div><span>责任方：</span><span>承运商-派件承运商</span></div>
						<div><span>异常描述：</span><span>少件</span></div>
						<div><span>处理结果：</span><span>未处理</span></div>
					</div>
					<div class="operateBtn">
						<div @click="abnormalSign">异常单签收</div>
					</div>
				</div>
		    </div>
		</div>
	</div>
</template>
<script type="text/javascript">
  import Scan from '@/components/Scan'
	export default {
		name:"ScanQueryOrderInfo",
		data(){
			return{
				AbnormalInfoData:{omsNo:1},//异常订单信息
		        loadingData: false,
		        AbnormalInfo_show:false,//异常订单信息是否显示
		        scanLabel:'',//当前扫描的标签号
		        LocateAbnormalScanLabel:'',//异常签收页面带回来的
			}
		},
	    components: {
	      'v-scan': Scan,  // 扫描组件
	    },
		mounted() {
		},
		created(){
			var that = this;
			if(this.$route.query.LocateAbnormalScanLabel){
		        this.LocateAbnormalScanLabel = this.$route.query.LocateAbnormalScanLabel;
		       
		        this.scanData( that.LocateAbnormalScanLabel)
		    }
		},
		methods:{
			// 扫描完成 调用
			scanData(data){

				var that = this;
				this.scanLabel = data;

				that.AbnormalInfo_show = true;

				var postData = {
					scanLabel: data,//标签号
			    };
			    console.log(postData)
			    return;

				this.httpRequest("queryShipmentData.do",postData,function(res){
					console.log(res);
					that.AbnormalInfoData = res.data;
					that.AbnormalInfo_show = true;
				})
			},
			// 异常单签收 按钮
			abnormalSign(){

				event.stopPropagation();
		
				var that = this;

				this.$router.push({
					name:"AbnormalScan",
					query:{
						omsNo : that.AbnormalInfoData.omsNo,
						LocateAbnormalScanLabel: that.scanLabel
					}
				})
			},
			// 返回上一页
			goprev(){

				this.$router.push({
					name:"ExceptionList",
				})
			}
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ScanQueryOrderInfo{
  	overflow: hidden;
  	height: 100%;
  	.container{
  		height: 100%;
  		padding-top: 90/50rem;
  		box-sizing:border-box;
  		margin-top: 0;
  		.content-box{
  			// background-color:#eee;
  			padding-bottom: 10/50rem;
  		}
  		.orderInfo{
			margin-top: 5/50rem;
			border-top: 3/50rem solid #ccc;
			.AbnormalItem{
			  	background-color:#fff;
				text-align: left;
				margin-bottom: 15/50rem;
				overflow: hidden;
				padding: 20/50rem;
				box-sizing:border-box;
				.nomalList{
					&>div{
						line-height: 55/50rem;
						overflow: hidden;
						&>div{
							float: left;
							width: 50%;
						}
					}
				}
				.operateBtn{	
					margin-top:20/50rem;
					display: flex;
					justify-content :  flex-end;
					div{
						flex-grow:1;
						max-width: 32%;
						margin-left: 15/50rem;
						text-align: center;
						background-color: @PublicColor;
						color: #fff;
						border-radius: 15/50rem;
						line-height: 60/50rem;
					}
					div:nth-child(1){
						margin-left:0;
					}
				}
			}
  		}
  	}
  }
</style>


