<template>
  <div class="MassTransfer">
	<header><i class="iconfont icon-xiangzuo1"  @click="$router.back(-1)"></i><span>签单上传</span></header>
	<div class="container borderLine">
		<!-- <div><span>配载单号：</span><span>{{shipmentCode}}</span></div> -->
		<div><span>订单号：</span><span>{{omsNo}}</span></div>
		<div><span>当前位置：</span><span>{{CurrentLocation}}</span></div>
		<div><span>当前城市：</span><span>{{CurrentCity}}</span></div>
	  <div class="Upload">
		<div class="uploadImg">
			<div class="LoadImg_head">拍照上传</div>
			<div>
				<el-upload :action = " HOST+'confirmationUpload.do'"  :limit="6" :multiple="true"  :data="uploadParams" ref='upload_ref1' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadErr" :on-success="uploadSuccessful"   :on-preview="handlePictureCardPreview" >
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</div>
		</div>
	  </div>
	</div>
	<div class="submitBtn">
		<p class="g_signBtn"  @click="submit">提交</p>
	</div>
	<div v-if="ifTips" class="msg_tips">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
  </div>
</template>
<script type="text/javascript">

	import $ from "jquery"
	
	export default{
		name:"MassTransfer",
		data(){
			return{
				dialogImageUrl: '',
		        dialogVisible: false,//是否放大预览照片
		        uploadParams:{},//上传图片时 额外的参数
		        tips_Msg:"",//提示信息
				ifTips:false,//提示信息是否显示
		        shipmentCode:'',//配载单号
		        omsNo:'',//订单号
		        CurrentLocation:'',//当前位置
		        CurrentCity:'',//当前位置所在城市
		        uploadSuccessNum:0,//上传成功数量
		        uploadTotalNum:0,//上传图片总数
		        uploadFailNum:0,//上传失败数量
			}
		},
		created(){

			if(this.$route.query.shipmentCode){
		        this.shipmentCode = this.$route.query.shipmentCode;
		    }

		    if(this.$route.query.omsNo){
		        this.omsNo = this.$route.query.omsNo;
		    }

			this.onPlusReady('e');

			// this.WaybillInfo = this.$store.state.Waybill.WaybillInfo.clickWaybillInfo[0];

			// this.uploadParams.tenantCode = this.WaybillInfo.tenantCode;//租户代码

			// this.uploadParams.code = this.$route.query.deliverNo_list;//运单号(交付传运单号过来，到车和中途反馈传配载单号过来)

			// this.uploadParams.codeType = "ORDER";//类型为 交付

			// this.uploadParams.codeId = this.$route.query.deliverNo_list;//交付时=deliveryId,其他两个=shipmentId

			this.uploadParams.cellphone = this.$store.state.userInfo.cellphone;//交付人手机号

		},
		methods:{
			// 提交
			submit(){

				var that = this;

				if(!$(".Upload>div.uploadImg:eq(0) ul>li").length){

					that.$alert('请上传图片', '提示', {
			            confirmButtonText: '确定'
		           	})

		           	return;
				}

				that.uploadTotalNum = $("div.uploadImg ul>li").length;
				console.log(that.uploadTotalNum )

				// 上传图片
				this.$refs.upload_ref1.submit();
			},
			//上传完成后（包含部分上传失败） 
			finishUpload(text){

				text = text?","+text:"";

				var that = this;
				this.$alert("签单上传成功"+text, '提示', {
		            confirmButtonText: '确定',
		            callback(){

		            	that.$router.back(-1);
		            }
		        })
			},
			// 文件上传成功时的钩子
		    uploadSuccessful  (response, file, fileList){

		        console.log(response)

		        var that = this;

		        this.uploadSuccessNum ++;

		        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
		          // 全部上传成功

		          that.finishUpload('');

		          return;

		        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
		          // 部分上传成功

		          that.finishUpload('部分图片上传失败');
		        }
		    },
		    // 文件上传失败时的钩子
		    uploadErr  (err, file, fileList){
		        console.log(err)
		        var that = this;

		        this.uploadFailNum++;

		        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
		          // 全部上传失败

		          that.uploadSuccessNum = 0;
		          that.uploadFailNum = 0;
		          that.uploadTotalNum = 0;
		          that.$emit('isLoading', false);

		          that.$alert("上传失败", '提示', {
		            confirmButtonText: '确定'
		          })
		          return;
		        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
		          // 部分上传失败

		          that.finishUpload('部分图片上传失败');
		        }
		    },
			// 预览放大图片
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			},
		}
	}
</script>
<style lang="less" scoped>
	.MassTransfer{
		overflow: hidden;
		.container{
			overflow: hidden;
			padding-bottom:150/50rem;
			&>div:not(.Upload){
				padding: 20/50rem;
				overflow: hidden;
				span{
					height: 50/50rem;
					line-height: 50/50rem;
				}
			}
			.Upload{
				.uploadImg{
					padding: 20/50rem;
					box-sizing: border-box;
					font-size: 30/50rem;
					border-top: 1/50rem solid #EDEDED;
					overflow: hidden;
					&>.LoadImg_head{
						float: none;
						text-align: center;
						margin-bottom:10/50rem; 
						span{
							color: #aaa;
						}
					}
					&>div{
						float: left;
					}
				}
			}
			
		}
	}
	.btnBox{
	  	width: 120/50rem;
	    height: 50/50rem;
	    line-height: 50/50rem;
	    background-color: #71b900;
	    color: #fff;
	    font-size: 28/50rem;
	    text-align: center;
	    border-radius: 8/50rem;
	    margin: 10/50rem auto;
	}
</style>


