<template>
  <div class="common-box">
    <v-header :headerName="headerName" :orderList="orderList"></v-header>
    <div class="content-box">
      <v-scan @scanData="scanData" :switchScan="switchScan" :loadingData="loadingData"></v-scan>
    </div>
    <!--<div class="order-level level1">-->
    <!--<div class="order-header">-->
    <!--子母单({{ orderList.length }})-->
    <!--</div>-->
    <!--<div class="order-list">-->
    <!--<div class="list-item" v-for="item in orderList">-->
    <!--{{item}}-->
    <!--<div class="close-button"></div>-->
    <!--</div>-->
    <!--</div>-->
    <!--<div class="no-data" v-show="orderList.length == 0">-->
    <!--<div class="right-bg"></div>-->
    <!--</div>-->
    <!--</div>-->
    <div class="order-level level2">
      <el-tabs v-model="activeName" :stretch="true" v-loading="loadingData">
        <el-tab-pane :label="'扫描标签(' + orderList.length + ')'" name="first">
          <div class="order-header">
            运单号({{ orderList.length }})
          </div>
          <div class="order-list">
            <div class="list-item" v-for="item in orderList">
              {{item.order}}
              <!--<div class="close-button" @click="deleteItem(item.order)"></div>-->
            </div>
          </div>
          <div class="no-data" v-show="orderList.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane :label="'历史标签('+ historyLabelNo.length + ')'" name="second">
          <div class="order-list">
            <div class="list-item" v-for="item in historyLabelNo">
              {{ item }}
            </div>
          </div>
          <div class="no-data" v-show="historyLabelNo.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane :label="'未扫描标签(' + notScanLabelNo.length + ')'" name="third">
          <div class="order-list">
            <div class="list-item" v-for="item in notScanLabelNo">
              {{ item }}
            </div>
          </div>
          <div class="no-data" v-show="notScanLabelNo.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <v-footer
        :count="orderList"
        :orderCount="orderCodeList.filter(item => {return item !== ''}).length"
        :countData="countData"
        @reflash="reflashData"
        @scanFun="switchScanFun"
        :orderList="cleanOrderList"
        :deliveryId='deliveryId'
        :transportLinks = 'transportLinks'
        :actualQty = 'actualQty'>
    </v-footer>
    <v-confirm ref="message"></v-confirm>
  </div>
</template>

<script>
  import Header from '@/components/header'
  import Scan from '@/components/Scan'
  import CommitFooter from '@/components/commitFooter'
  import Confirm from '@/components/confirm'
  import {getTheOrderNumber} from "../../../api/api";

  export default {
    name: "delivery-scan",
    components: {
      'v-header': Header, // 头部组件
      'v-scan': Scan,  // 扫描组件
      'v-footer': CommitFooter,  // 提交组件
      'v-confirm': Confirm
    },
    data() {
      return {
        activeName: 'first',
        headerName: '签收扫描',
        orderCount: 0, // 订单号数量统计
        countData: [], // 需要提交给后台的对象数组
        orderList: [], // 运单号列表
        orderCodeList: [], // 订单号列表
        cleanOrderList: [], // 只有运单号的数组

        scanLabelNo: [], // 本次扫描的标签好列表
        historyLabelNo: [], // 历史标签号数据数组
        notScanLabelNo: [], // 未扫描标签号数据

        loadingData: false,
        linkNode: '',
        switchScan: true,
        shipmentCode:'',//配载单号
        deliveryId:'',//运单id
        omsNo:'',//订单号
        transportLinks:'',//扫描环节
        scanNumber:0,//已扫描数量
        actualQty:0,//订单总件数
      }
    },
    // http://localhost:8082/#/DeliveryScan
    mounted() {
      switch (this.$route.path) {
        case '/DeliveryScanXH':
          this.linkNode = '10'
          break
        case '/DeliveryScan':
          this.linkNode = '10'
          break
        case '/TransitScan':
          this.linkNode = '20'
          break
        case '/DeliveryOrderPickScan':
          this.linkNode = '30'
          break
        case '/ReceiptScan':
          this.linkNode = '40'
          break
        default:
          this.linkNode = '10'
          break
      }
    },
    created(){
      
      if(this.$route.query.shipmentCode){
        this.shipmentCode = this.$route.query.shipmentCode;
      }
      
      if(this.$route.query.deliveryId){
        this.deliveryId = this.$route.query.deliveryId;
      }
      if(this.$route.query.omsNo){
        this.omsNo = this.$route.query.omsNo;
      }
      
      if(this.$route.query.transportLinks){
        this.transportLinks = this.$route.query.transportLinks;
      }

      if(this.$route.query.scanNumber){
        this.scanNumber = this.$route.query.scanNumber;
      }

      if(this.$route.query.actualQty){
        this.actualQty = this.$route.query.actualQty;
      }
    },
    methods: {
      switchScanFun(data) {
        if(data) {
          this.switchScan = true // 打开摄像头
        }else {
          this.switchScan = false // 关闭摄像头
        }
      },
      // 提示框
      userBehaviorFun() {

      },
      /**
       * 删除单条不要的数据
       **/
      deleteItem(key) {
        this.orderList = this.orderList.filter(item => {
          return item.order !== key
        })
        let arr = []
        for (let i in this.orderList) {
          arr.push(this.orderList[i]['orderBooting'])
        }

        // 去重
        function unique(list) {
          return Array.from(new Set(list))
        }

        this.orderCodeList = unique(arr)
        this.cleanOrderList = []
        for (let item in this.orderList) {
          this.cleanOrderList.push(this.orderList[item].order)
        }
      },
      /**
       * 提交成功清空数据
       **/
      reflashData() {
        this.countData = []
        this.orderList = []
        this.orderCodeList = []
        this.cleanOrderList = []

        this.scanLabelNo= [] // 本次扫描的标签好列表
        this.historyLabelNo= [] // 历史标签号数据数组
        this.notScanLabelNo= []// 未扫描标签号数据
      },
      /**
       * 从子组件{scan.vue}获取传过来的数据
       * @param data 标签号
       */
      scanData(data) {
        let _this = this
        // if(this.orderList.length !== 0) {
        //   if(JSON.stringify(this.orderList).indexOf(JSON.stringify(data)) == -1) {
        //     let params = {
        //       scanLabel: data
        //     }
        //     this.getTheOrderNum(params, data)
        //   }else {
        //     alert('已存在该运单号')
        //   }
        // }else {
        //   let params = {
        //     scanLabel: data
        //   }
        //   this.getTheOrderNum(params, data)
        // }

        // 如果默认的历史标签数据与未扫描数据数组为空，说明这是新开的界面，需要请求比对数据
        if (this.historyLabelNo.length === 0 && this.notScanLabelNo.length === 0 && this.orderList.length === 0) {
          let params = {
            scanLabel: data,
            linkNode: this.linkNode,
            omsNo:_this.omsNo
          }
          this.getTheOrderNum(params, data)
        } else { // 历史数据与未扫描数据数组不为空，有比对数据，先进行数据对比
          _this.step0(data).then(data0 => { // 当前扫描中的列表数据是否有相同数据
            if(data0.length !== 0) {
              _this.$refs.message.show('该标签号已扫描过!', {
                type: 'alert',
                confirmText: '确定',
                titleText: '请注意',
              })
            }else {
              _this.step1(data).then(data1 => {
                if (data1.length !== 0) { // 在旧历史数据中有关联的标签号，提示用户保存过该数据
                  _this.$refs.message.show('该标签号已扫描保存过!', {
                    type: 'alert',
                    confirmText: '确定',
                    titleText: '请注意',
                  })
                } else {
                  _this.step2(data).then(data2 => {
                    if (data2.length !== 0) { // 在未保存数据中有相应的标签号，直接在标签号列表添加相应的标签号
                      this.orderList.push({
                        order: data,
                        orderBooting: '',
                        time: this.getTheTimeNow()
                      })
                      // 删除添加到扫描标签的未扫描标签
                      this.notScanLabelNo = this.notScanLabelNo.filter(item => {
                        return item !== data
                      })
                    } else {
                      let params = {
                        scanLabel: data,
                        linkNode: this.linkNode,
                        omsNo:_this.omsNo
                      }
                      this.getTheOrderNum(params, data)
                    }
                  })
                }
              })
            }
          })
        }
      },
      /**
       * 获取当前扫描时候的数据时间
       **/
      getTheTimeNow() {
        const date = new Date()
        var year = date.getFullYear()
        var month = checkVal(date.getMonth() + 1)
        var day = checkVal(date.getDay())
        var hour = checkVal(date.getHours())
        var min = checkVal(date.getMinutes())
        var sec = checkVal(date.getSeconds())

        function checkVal(val) {
          if (val < 10) {
            return '0' + val
          } else {
            return val
          }
        }

        return `${year}-${month}-${day} ${hour}:${min}:${sec}`
      },
      /**
       * 数组去重方法
       **/
      distinct(a, b) {
        let arr = a.concat(b)
        let result = []
        let obj = {}

        for (let i of arr) {
          if (!obj[i]) {
            result.push(i)
            obj[i] = 1
          }
        }
        return result
      },
      // 0. 检测扫描标签中是否存在正在扫描的标签，有的话提示已扫描，否则进入下一步
      step0(val) {
        let _this = this
        return new Promise(resolve => {
          let arr = _this.orderList.filter(item => {
            return item.order == val
          })
          resolve(arr)
        })
      },
      // 1. 检测比对历史数据以及未扫描数据，排查是否存在该标签号
      step1(val) {
        let _this = this
        return new Promise((resolve, reject) => {
          let arr = _this.historyLabelNo.filter(item => {
            return item == val
          })
          resolve(arr)
        })
      },
      // 2. 当比对历史数据不存在扫描到的标签号的时候再查询未查询扫描信息的列表进行比对
      step2(val) {
        let _this = this
        return new Promise((resolve, reject) => {
          let arr = _this.notScanLabelNo.filter(item => {
            return item == val
          })
          resolve(arr)
        })
      },
      /**
       * 调用接口{ getTheOrderNumber }获取运单的订单号
       * @param params 请求的传参{ 扫描的标签号 }，data { 标签号 }
       */
      getTheOrderNum(params, data) {
        let _this = this
        this.loadingData  = true
        getTheOrderNumber(params).then(res => {
          if (res.data.status === '1') { // 状态值为1成功，否则抛出错误信息
            this.loadingData  = false
            let obj = res.data.data

            function reSetData() {
              return new Promise(resolve => {
                // 首先整合请求到的数据列表
                _this.historyLabelNo = _this.historyLabelNo.concat(obj.historySubmitLabelNo)
                _this.notScanLabelNo = _this.notScanLabelNo.concat(obj.notScanLabelNo)
                // 数组去重
                function unique(list) {
                  return Array.from(new Set(list))
                }
                _this.historyLabelNo = unique(_this.historyLabelNo)
                _this.notScanLabelNo = unique(_this.notScanLabelNo)
                resolve(true)
              })
            }

            reSetData().then(resolve => {
              if(resolve) {
                // 逻辑运行
                _this.step1(data).then(data1 => {
                  if (data1.length !== 0) { // 1. 当匹配到的数据存在的时候，提示用户该标签号曾经扫描保存过
                    _this.$refs.message.show('该标签号已扫描保存过!', {
                      type: 'alert',
                      confirmText: '确定',
                      titleText: '请注意',
                    })
                    this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
                  } else {
                    _this.step2(data).then(data2 => { // 2. 当历史保存数据中没搜索到相关记录，再查找未扫描的标签号是否存在这个正在扫描的标签号
                      if (data2.length !== 0) { // 在未扫描数据中匹配到该未扫描标签号，保存起来展示到列表中
                        this.orderList.push({
                          order: data,
                          orderBooting: obj.omsNo,
                          time: this.getTheTimeNow()
                        })
                        // 删除添加到扫描标签的未扫描标签
                        this.notScanLabelNo = this.notScanLabelNo.filter(item => {
                          return item !== data
                        })
                        this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
                      } else { // 保存的数据与未保存的历史数据都不存在该记录的话则是因为不存在该标签号，提示用户标签号错误
                        _this.$refs.message.show('标签号错误，请重新扫描!', {
                          type: 'alert',
                          confirmText: '确定',
                          titleText: '请注意',
                        })
                      }
                    })
                  }
                })
              }
            })

            // this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
            // this.orderList.push({
            //   order: data,
            //   orderBooting: res.data.omsNo,
            //   time: this.getTheTimeNow()
            // })

            this.cleanOrderList = []
            for (let item in this.orderList) {
              this.cleanOrderList.push(this.orderList[item].order)
            }

            // 返回参数成功，则把订单号统计进{ orderCodeList }中调用方法{ distinct }进行去重
          } else {
            alert(res.data.Msg)
            this.loadingData  = false
          }
        }).catch(err => {
          alert(err)
          this.loadingData  = false
        })
      }
    }
  }
</script>

<style scoped>

</style>



