<template>
  <div class="scan-box">
    <div id="bcid" ref="scan">
      <div></div>
  <!--     <div class="flash-button">
        <img :src="imgSelect">
        <div class="text-bottom">触摸扫码区打开闪光灯</div>
      </div> -->
    </div>
    <div class="input-order-number">
      <img src="../assets/input.png">
      <input type="text" v-model="code" placeholder="手动输入标签号...">
      <div class="button-add" @click="commitInput">确认</div>
    </div>
  </div>
</template>

<script>
  let scan = null
  var ws = null
  var wo = null
  import { bus } from '../utils/bus'
  export default {
    name: "scan",
    props: ['loadingData', 'switchScan'],
    watch: {
      loadingData(newData, oldData) {
        if(newData) {
          this.cancelScan()
        }else {
          this.startScan()
        }
      },
      switchScan(newData, oldData) {
        if(!newData) {
          this.cancelScan()
          this.closeScan()
        }else {
          this.startRecognize()
          this.startScan()
        }
      }
    },
    data() {
      return {
        codeUrl: '',
        code: '200222102578_10',
        activeFlash: false,
        imgSelect: require('../assets/flash.png'),
        scanState: false
      }
    },
    created() {

    },
    mounted() {
      this.startRecognize() // 页面挂载完成调用开启控件
      this.startScan()
      bus.$on('flashActive', data => {
        if (!window.plus) return;
        if(data) {
          scan.setFlash( false );
        }else {
          scan.setFlash( true );
        }
      })
      // 监听bus 开关
      bus.$on('switchScan', data => {
        if(data){
          this.startRecognize()
          this.startScan()
        }else {
          this.cancelScan()
          this.closeScan()
        }
      })
    },
    beforeDestroy() {
      this.cancelScan()
      this.closeScan()
    },
    methods: {
      /**
       * 点击打开闪光灯
       */
      setTheFlash() {
        if (!window.plus) return;
        if(this.activeFlash) {
          scan.setFlash( false );
          this.activeFlash = false
        }else {
          scan.setFlash( true );
          this.activeFlash = true
        }
      },
      /**
       * 提交输入框输入的内容
       **/
      commitInput() {
        if(this.code !== '') {
          this.$emit('scanData', this.code)
        }
      },
      /**
       * 创建扫描控件时候调用的方法
       */
      startRecognize() {
        let that = this;
        if (!window.plus) return;
        var styles = {frameColor: "#3a5efd",scanbarColor: "#3a5efd"};
        scan = new plus.barcode.Barcode('bcid', [plus.barcode.CODE128], styles);
        scan.onmarked = onmarked;

        function onmarked(type, result) {
          switch (type) {
            // 二维码
            case plus.barcode.QR:
              type = 'QR';
              break;
            // 一维条形码简版
            case plus.barcode.EAN8:
              type = 'EAN8';
              break;
            // 一维条形码码标准版
            case plus.barcode.EAN13:
              type = 'EAN13';
              break;
            default:
              type = '其它' + type;
              break;
          }
          result = result.replace(/\n/g, '');
          that.codeUrl = result;
          // alert('类型:'+type + ',内容:' + result); // 调试用获取条形码信息

          that.$emit('scanData', result) // 把获取到的条形码传给父组件
          
          that.cancelScan();
          setTimeout(_ => {
            that.startScan()
          }, 1000)
        }
      },
      /**
       * 开始扫描时候调用的方法
       */
      startScan() {
        if (!window.plus) return;
        scan.start();
      },
      /**
       * 结束扫描时候调用的方法
       */
      cancelScan() {
        if (!window.plus) return;
        scan.cancel();
      },
      /**
       * 关闭扫描控件时候调用的方法
       */
      closeScan() {
        if (!window.plus) return;
        scan.close();
      }
    }
  }
</script>

<style scoped lang="less">
  @import "publicLess.less";
  .scan-box{
    background-color: rgba(0,0,0,0.0);
  }
  .scan-box {
    width: 100%;
    height: 400/50rem;
    z-index: 1;
  }
  #bcid{
    width: 100%;
    height: 320/50rem;
    top: 90/50rem;
    left: 0;
    position:fixed;
    background-color: @PublicColor;
    &>div{
      height: 320/50rem;
    }
  }
  .scan {
    height: 100%;
  }
  .input-order-number{
    width: 100%;
    display: flex;
    position: absolute;
    bottom: 0;
    flex-wrap: nowrap;
    background-color:#F4F4F4;
  }
  .input-order-number img{
    width: 80/50rem;
    padding:12/50rem;
    box-sizing: border-box;
    height: 80/50rem;
    line-height: 80/50rem;
  }
  .input-order-number input{
    flex: 1;
    border: none;
    line-height:  80/50rem;
    height: 80/50rem;
    padding-left: 10/50rem;
    box-sizing: border-box;
    outline: none;
  }
  .input-order-number .button-add{
    width: 80/50rem;
    height: 80/50rem;
    font-size: 28/50rem;
    line-height: 80/50rem;
    margin:0 10/50rem;
    text-align: center;
    background-color: @PublicColor;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
  }
</style>



