<template>
	<div class="ReportForms">
		<header><span>报表</span></header>
		<div class="container">
			<div v-if="scanIF" class="scan">
				<input>
				<span @click="sys_click">扫一扫</span>
			</div>
		</div>
		<!-- 页面数据为空时 -->
		<div    class="NoData" >
			<div>
				<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
				<div>{{is_NoData_text}}</div>
			</div>

		</div>
		<FooterIndex/>
	</div>
</template>
<script type="text/javascript">
import FooterIndex from '../../components/footer.vue'

import wx from 'weixin-js-sdk'

	export default{
		name:"ReportForms",
		data(){
			return{
				is_NoData_text:'没有数据',
				scanIF:false
			}
		},
		components:{
		  	FooterIndex
		},
		methods:{
			sys_click(){
	        //这里【url参数一定是去参的本网址】
	        $.get("获取微信认证参数的地址?url=去参的本网页地址", function(data){
	        var jsondata=$.parseJSON(data);
	            wx.config({
	                // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
	                debug: true,
	                // 必填，公众号的唯一标识
	                appId: jsondata.model.appId,
	                // 必填，生成签名的时间戳
	                timestamp: "" + jsondata.model.timestamp,
	                // 必填，生成签名的随机串
	                nonceStr: jsondata.model.nonceStr,
	                // 必填，签名
	                signature: jsondata.model.signature,
	                // 必填，需要使用的JS接口列表，所有JS接口列表
	                jsApiList: ['checkJsApi', 'scanQRCode']
	            });
	        });
	        wx.error(function (res) {
	            alert("出错了：" + res.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
	        });
	 
	        wx.ready(function () {
	            wx.checkJsApi({
	                jsApiList: ['scanQRCode'],
	                success: function (res) {
	 
	                }
	            });
	 
	                wx.scanQRCode({
						needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
						scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
						success: function (res) {
							var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
						}
					});
	 
	        }); 
	        }

		}
	}
</script>
<style lang="less" scoped>
	.ReportForms{
		overflow: hidden;
		.scan{
			margin-top: 20/50rem;
			line-height: 60/50rem;
			input{
				margin:0 20/50rem;
				float: left;
				height: 60/50rem;
			}
			span{
				background-color: #5965D8;
				color: #fff;
				padding: 0 20/50rem;
				border-radius: 8/50rem;
				float: left;
			}
		}
	}
</style>


