<template>
  <div class="commit-footer">
    <div class="left-count">
      <div class="top">
        <!-- 订单数量:<i>{{ orderCount }}</i>票 -->
      </div>
      <div class="bottom">
        运单数量:<i>{{ count.length }}</i>件
      </div>
    </div>
    <div></div>
    <div v-if='!hideBitch' class="commit-button BatchCommit" @click='BatchCommit'>批量提交</div>
    <div class="commit-button" @click="checkForCommit">提交</div>
    <v-confirm ref="myConfirm" @userBehavior="userBehaviorFun"></v-confirm>

    <el-dialog title="上传图片"
               class="img-dialog"
               :visible.sync="commitImgDialog"
               :close-on-click-modal="false"
               :show-close="false"
               append-to-body width="90%">
      <el-upload :action="HOST + 'scanLabel.do'"
                 ref="upload"
                 class="avatar-uploader"
                 :data="params"
                 name="fileupload"
                 :multiple="false"
                 :show-file-list="false"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload"
                 :on-change="getTheFile"
                 :auto-upload="true">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeImgInput">取 消</el-button>
        <!--<el-button type="primary" @click="commitImg">确 定</el-button>-->
      </span>
    </el-dialog>
    <!-- 批量提交修改数量弹出框 -->
    <div class="BatchCommitPopBox" v-show='BatchCommit_show'>
      <div class="BatchCommit_">
        <div class="BatchCommit_head">请填写要批量提交的数量</div>
        <div><span>订单总件数：</span><span>{{actualQty}}</span></div>
        <div class="BatchCommit_content">
          <input type="number" v-model='BatchCommitNumber'>
        </div>
        <div class="BatchCommit_btn">
          <div class="commit-button" @click='BatchCommitCancel'>取消</div>
          <div class="commit-button" @click='BatchCommitConfirm'>批量提交</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { addOrderList } from "../api/api";
  import Confirm from '../components/confirm'
  import { bus } from '../utils/bus'
  import Axios from "axios"
  let location = null
  export default {
    name: "commit-footer",
    components: {
      'v-confirm': Confirm
    },
    data() {
      return {
        params: {
          params: [],
          longitude: '', // 经度
          latitude: '', // 纬度
        },
        CurrentCity:'',//当前城市
        CurrentLocation:'',//当前地址
        cleanOrderList: [],
        commitImgDialog: false,
        imageUrl: null,
        imgObj: null,
        BatchCommit_show:false,//批量提交修改数量弹出框 是否出现
        BatchCommitNumber:'',//批量提交数量
      }
    },
    props: [
      'count', // 运单数量
      'orderCount', // 订单数量
      'countData',
      'orderList',
      'cacheResponseData',
      'deliveryId',
      'hideBitch',
      'transportLinks',//扫描环节
      'actualQty',//订单总件数
    ],
    mounted() {
      this.getTheLocation() // 获取APP经纬度

    },
    methods: {
      // 批量提交弹出框出现
      BatchCommit(){

        if(this.count.length === 0) {

          return;
        }
        this.BatchCommit_show = true;

        this.$emit('scanFun', false) // 关闭摄像头
      },
      // 批量提交弹出框 取消按钮
      BatchCommitCancel(){
        this.BatchCommit_show = false;
        this.$emit('scanFun', true) // 打开摄像头
      },
      BatchCommitConfirmitem(){
        var that = this;


        var path = this.$route.path
        var linkNode = '' // 需要传参的linkNode类别

        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }

        var postData = {
          deliveryId: that.deliveryId,//运单id
          labelNumber: that.BatchCommitNumber,//数量
          transportLinks:linkNode,//扫描环节
        };

        console.log(postData)

        Axios.post(that.HOST+"batchScanLabel.do",{"params": JSON.stringify(postData)})
        .then(function(res){
          console.log(res)
          
          if(res.data.status=='1'){

            that.$alert('批量提交成功', '提示', {
              confirmButtonText: '确定',
              callback(){
                that.$router.go(-1)
              }
            })

          }else{

            var Msg;
            if(res.data.Msg){

              Msg = res.data.Msg;
            }else {

              Msg = "批量提交失败";
            }
            that.$emit('reflash', true);
            that.$emit('scanFun', true);
            alert(Msg);
          }
        })
        .catch(function(res){

          that.$emit('reflash', true);
          that.$emit('scanFun', true);
          alert("批量提交失败");
        })
      },
      // 批量提交 确认提交
      BatchCommitConfirm(){
        var that = this;
        if(!this.BatchCommitNumber){
          return;
        }

        const _this = this
        let path = this.$route.path
        let linkNode = '' // 需要传参的linkNode类别
        const scanPerson = this.$store.state.userInfo.userName // 扫描人的名称 *
        const supplierId = this.$store.state.userInfo.orgnizationId // 承运商的ID
        const supplierName = this.$store.state.userInfo.orgnizationName // 承运商的名称
        const longitude = this.longitude
        const latitude = this.latitude
        const address = this.CurrentLocation;
        const city = this.CurrentCity;
        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }
        let params = []
        for(let key in this.count) {
          let obj = {}
          obj.labelNo = this.count[key].order
          obj.linkNode = linkNode
          obj.scanPerson = scanPerson
          obj.scanTime = this.count[key].time
          obj.supplierId = supplierId
          obj.supplierName = supplierName
          obj.longitude = longitude
          obj.latitude = latitude
          obj.address = address
          obj.city = city

          params.push(obj)
        }

        let displayWtList = []
        for(let i in this.cacheResponseData) {
          let cacheObj = {}
          let item = this.cacheResponseData[i]
          cacheObj.omsNo = item.omsNo
          cacheObj.displayWt = item.displayWt
          cacheObj.linkNode = linkNode

          displayWtList.push(cacheObj)
        }

        this.cleanOrderList = []
        for (let item in this.count) {
          this.cleanOrderList.push(this.count[item].order)
        }
        if(linkNode === '10') {

          if(this.$route.path == '/DeliveryScanXH'){
            
            this.params = {
              "params" : JSON.stringify(params),
              "labelNo": JSON.stringify(this.cleanOrderList),
              "deliveryId":that.deliveryId
            }
            console.log(this.params)
            addOrderList(this.params).then(res => {
              console.log(res)
              if(res.data.status == '1') {
                console.log(res)
                // alert(res.data.Msg)
                that.BatchCommitConfirmitem();
                
              }else {
                console.log(res)
                alert(res.data.Msg)
                this.$emit('scanFun', true)
              }
            })

          }else{
            function getLink10Data() {
              return new Promise(resolve => {
                _this.params = { // 提货扫描需要增加实际重量以及上传图片
                  "params" : JSON.stringify(params),
                  "labelNo": JSON.stringify(_this.cleanOrderList),
                  "omsNo_displayWt": JSON.stringify(displayWtList),
                  "deliveryId":that.deliveryId
                }
                resolve(_this.params)
              })
            }
            getLink10Data()
          }
        } else if(linkNode === '40') {
          function getLink40Data() {
            return new Promise(resolve => {
              _this.params = {
                "params" : JSON.stringify(params),
                "labelNo": JSON.stringify(_this.cleanOrderList),
                "deliveryId":that.deliveryId
              }
              resolve(_this.params)
            })
          }

          getLink40Data()
          // .then(data => {
          //   this.$refs.upload.submit()
          // })
        } else {
          this.params = {
            "params" : JSON.stringify(params),
            "labelNo": JSON.stringify(this.cleanOrderList),
            "deliveryId":that.deliveryId
          }
          addOrderList(this.params).then(res => {
              console.log(res)
            if(res.data.status == '1') {
              console.log(res)
              // alert(res.data.Msg)
              // this.$emit('reflash', true)
              // this.$emit('scanFun', true)

              that.BatchCommitConfirmitem();

            }else {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('scanFun', true)
            }
          })
        }
      },
      getTheFile(file) {
        console.log(file)
        console.log(this.params)
        this.imageUrl = file.url
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 10MB!');
        }
        return isLt2M;
      },
      handleAvatarSuccess(res, file) {
        console.log(res)
        this.imageUrl = URL.createObjectURL(file.raw);
        if (res.status === '1') {
          console.log(res)
          alert(res.Msg)
          this.commitImgDialog = false
          this.imageUrl = ''
          this.params = {}
          this.$refs.upload.clearFiles()
          this.$emit('reflash', true)

          // bus.$emit('switchScan', true)
          this.$emit('scanFun', true) // 打开摄像头
        } else {
          alert(res.Msg)
        }
      },
      closeImgInput() {
        this.commitImgDialog = false
        // bus.$emit('switchScan', true) // 打开摄像头
        this.$emit('scanFun', true) // 打开摄像头
      },
      commitImg() {
        this.commitImgDialog = false
        this.$refs.myConfirm.show('是否提交数据？', {
          type: 'confirm',
          cancelText: '取消',
          confirmText: '确定',
          titleText: '消息提示',
          data: this.count
        })
      },
      selectFile(item) {
        this.imgUrl = item
        this.setTheFormData()
      },
      setTheFormData() {
        const _this = this
        let formData = new FormData()
        formData.append('data', this.imgUrl.srcElement.files[0])
        this.imgObj = formData.get('data')

        let reader = new FileReader()
        reader.readAsDataURL(formData.get('data'))
        reader.onload = function(e) {
          _this.$refs.img.src = e.target.result
        }
      },
      /**
       * 获取用户的经纬度信息
       **/
      getTheLocation() {
        var that = this
        if (!window.plus) return;

        location = new plus.geolocation.getCurrentPosition(p => {

          that.longitude = p.coords.longitude
          that.latitude = p.coords.latitude

          if(p.coords.longitude && p.coords.latitude){

            AMap.plugin('AMap.Geocoder', function() {

              var geocoder = new AMap.Geocoder({
                // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                city: '010'
              })

              var lnglat = [p.coords.longitude, p.coords.latitude];

              geocoder.getAddress(lnglat, function(status, result) {

                console.log(status)
                console.log(result)

                if (status === 'complete' && result.info === 'OK') {
                  // result为对应的地理位置详细信息
                  that.CurrentCity = result.regeocode.addressComponent.city?result.regeocode.addressComponent.city:result.regeocode.addressComponent.province;
                  that.CurrentLocation = result.regeocode.formattedAddress;

                }else{

                  alert('位置信息解析失败');
                }
              })
            })

          }else{
            alert('获取定位失败');
          }
        }, err => {
          console.log(err)
          alert(err.message)
        })
      },
      userBehaviorFun(type, data) {
        if(type !== 'clickCancel') {
          this.commitData()
        }else {
          this.$emit('scanFun', true)
        }
      },
      // 判断路由，提货扫描以及签收扫描弹出弹窗
      checkForCommit() {
        if(this.count.length !== 0) {
          // this.getTheLocation()
          if (this.$route.path === '/DeliveryScan' || this.$route.path === '/ReceiptScan') {
            this.commitImgDialog = true
            // bus.$emit('switchScan', false) // 关闭摄像头
            this.$emit('scanFun', false) // 关闭摄像头
            this.commitData()
          } else {
            this.$refs.myConfirm.show('是否提交数据？', {
              type: 'confirm',
              cancelText: '取消',
              confirmText: '确定',
              titleText: '消息提示',
              data: this.count
            })
            // bus.$emit('switchScan', false) // 关闭摄像头
            this.$emit('scanFun', false) // 关闭摄像头
          }
        }
      },
      /**
       * 数据提交
       */
      commitData() {

        var that = this;
        const _this = this
        let path = this.$route.path
        let linkNode = '' // 需要传参的linkNode类别
        const scanPerson = this.$store.state.userInfo.userName // 扫描人的名称 *
        const supplierId = this.$store.state.userInfo.orgnizationId // 承运商的ID
        const supplierName = this.$store.state.userInfo.orgnizationName // 承运商的名称
        const longitude = this.longitude
        const latitude = this.latitude
        const address = this.CurrentLocation;
        const city = this.CurrentCity;
        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }
        let params = []
        for(let key in this.count) {
          let obj = {}
          obj.labelNo = this.count[key].order
          obj.linkNode = linkNode
          obj.scanPerson = scanPerson
          obj.scanTime = this.count[key].time
          obj.supplierId = supplierId
          obj.supplierName = supplierName
          obj.longitude = longitude
          obj.latitude = latitude
          obj.address = address
          obj.city = city

          params.push(obj)
        }

        let displayWtList = []
        for(let i in this.cacheResponseData) {
          let cacheObj = {}
          let item = this.cacheResponseData[i]
          cacheObj.omsNo = item.omsNo
          cacheObj.displayWt = item.displayWt
          cacheObj.linkNode = linkNode

          displayWtList.push(cacheObj)
        }

        this.cleanOrderList = []
        for (let item in this.count) {
          this.cleanOrderList.push(this.count[item].order)
        }
        if(linkNode === '10') {

          if(this.$route.path == '/DeliveryScanXH'){
            
            this.params = {
              "params" : JSON.stringify(params),
              "labelNo": JSON.stringify(this.cleanOrderList),
              "deliveryId":that.deliveryId
            }
            addOrderList(this.params).then(res => {
              console.log(res)
              if(res.data.status == '1') {
                console.log(res)
                alert(res.data.Msg)

                this.$emit('reflash', true)
                this.$emit('scanFun', true)
              }else {
                console.log(res)
                alert(res.data.Msg)
                this.$emit('scanFun', true)
              }
            })

          }else{
            function getLink10Data() {
              return new Promise(resolve => {
                _this.params = { // 提货扫描需要增加实际重量以及上传图片
                  "params" : JSON.stringify(params),
                  "labelNo": JSON.stringify(_this.cleanOrderList),
                  "omsNo_displayWt": JSON.stringify(displayWtList),
                  "deliveryId":that.deliveryId
                }
                resolve(_this.params)
              })
            }
            getLink10Data()
          }
        } else if(linkNode === '40') {
          function getLink40Data() {
            return new Promise(resolve => {
              _this.params = {
                "params" : JSON.stringify(params),
                "labelNo": JSON.stringify(_this.cleanOrderList),
                "deliveryId":that.deliveryId
              }
              resolve(_this.params)
            })
          }

          getLink40Data()
          // .then(data => {
          //   this.$refs.upload.submit()
          // })
        } else {
          this.params = {
            "params" : JSON.stringify(params),
            "labelNo": JSON.stringify(this.cleanOrderList),
            "deliveryId":that.deliveryId
          }
          addOrderList(this.params).then(res => {
              console.log(res)
            if(res.data.status == '1') {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('reflash', true)
              this.$emit('scanFun', true)
            }else {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('scanFun', true)
            }
          })
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @import "publicLess.less";
  .my-confirm {
    
  }
</style>



