<template>
  <div class="WaybillTrack">
    <header><i class="iconfont icon-xiangzuo1"   @click="$router.back(-1)"></i><span>路由详情</span></header>
    <div class="container">
      <div class='orderInfo' v-if='orderNum'>
        <div>
          <span>配载单号：</span>
          <span>{{orderNum}}</span>
          <icon v-if='orderNum' @click.stop='CopyNum(orderNum)' class='iconfont icon-fuzhi'></icon>
        </div>
        <!-- <div><span>订单数量：</span><span>{{orderSF.orderQty}}</span> </div> -->
        <!-- <div><span>始发城市：</span><span>{{orderSF.issuePartyCity}}</span> </div>
        <div><span>终点城市：</span><span>{{orderSF.receivePartyCity}}</span> </div> -->
      </div>
      
      <div class='TrackingInfo'>
        <div class='TrackDetailList' v-for="(itemName,index) in WaybillTrackList" :id="index"  :key='index'>
          <div class='TrackOfTime'>
            <div>{{itemName.dataTime}}</div>
            <div>{{itemName.detailTime}}</div>
          </div>
          <div class='TraceListStyle_outter'>
            <div class='TraceListStyle'>
              <div class='TraceListStyle_lineTop'></div>
              <div class='TraceListStyle_dot'>
                <div></div>
                <img src='/static/images/TrackLine_Up.png'></img>
              </div>
              <div class='TraceListStyle_lineBottom'></div>
            </div>
          </div>
          <div class='TrackProgress'>
            <span>{{itemName.routeDescribe}}</span>
          </div>
        </div>
      </div>
    </div>
    
    <div class='noData' v-if="!WaybillTrackList.length">
      <i  class="iconfont icon-rBEhWVIxcboIAAAAAAHkLwWBmsAADFGgISYAAeRHjpg"></i>
      <div>该单号暂无物流进展</div>
    </div>
  </div>
</template>
<script>
  export default{
  	data(){
  	  return{
        orderNum:'',//订单号
        orderSF:{},//订单信息
        WaybillTrackList: [],//追踪信息列表
        code: '',//配载单号
        carShow:false,//物流车 显示
  	  }
  	},
    created() {

      var that = this;


      if(this.$route.query.shipmentCode){

        this.orderNum = this.$route.query.shipmentCode;
        
      }
   
      if(!that.orderNum.length){

        that.$alert("未获取到订单号,请稍后重试", '异常提示', {
          confirmButtonText: '确定',
          callback: action => {
            that.$router.back(-1)
          }
        })
        return;
      }

        var data = {
          'omsNo':that.orderNum,//订单号
        }

        console.log(data)
        this.httpRequest("getOrderRoute.do",postData,function(res){

          console.log(res)

          that.orderSF = res.data.orderSF;
          var WaybillTrackList = res.data.orderTrack;

          for (var j = 0; j < WaybillTrackList.length; j++){

            WaybillTrackList[j].dataTime = WaybillTrackList[j].operateTime.substring(0, 10);
            WaybillTrackList[j].detailTime = WaybillTrackList[j].operateTime.substring(11,16);
          }

          that.WaybillTrackList = WaybillTrackList.sort(function sequence(a, b) {
            return Date.parse(b.operateTime) - Date.parse(a.operateTime);//时间倒序
          })
        });
    
    },
  	methods:{
      a(){

        var content = [
            {
                parameters:{
                  "属性/变量A":value1,
                  "计算方式":value2,
                  "比较":value3,
                  "阈值":value4,
                },
                // 条件类型 match-匹配规则 dataList-名单规则 compare-比较规则 range-区间规则
                ruleType:"match",
            },
            {
                parameters:{
                  "key":'计算方式',
                  "value":value2
                },
                ruleType:"dataList",
                
            },
            {
                parameters:{
                  "key":'比较',
                  "value":value3
                },
                ruleType:"compare",
                
            },
            {
                parameters:{
                  "key":'阈值',
                  "value":value4
                },
                ruleType:"range",
                
            }
        ]
      }
     /* // 复制运单号功能
      CopyNum(textContent){
        wx.setClipboardData({
          data: textContent,
          success (res) {
          }
        })
      },*/
  	}
  }
</script>
<style lang="less" scoped>
  .WaybillTrack{
    width:100%;
    min-height: 100%;
    position:relative;
    background-color: #F7F7F7;
    padding:0 20/50rem;
    box-sizing:border-box;
    text-align: center;
    overflow: hidden;
    .orderInfo{
      margin-top: 20/50rem;
      border-radius:10/50rem;
      width: 100%;
      background-color:#fff;
      &>div{
        text-align: left;
        padding-left: 20/50rem;
        line-height: 60/50rem;
        box-sizing: border-box;
        .iconfont{
          font-size: 30/50rem;
          margin-left: 20/50rem;
        }
      }
    }
    /*追踪信息 */
    .TrackingInfo{
      width:100%;
      border-radius:10/50rem;
      background-color:#fff;
      font-size: 26/50rem;
      color: #2F2F2F;
      margin: 20/50rem 0;
      &>.TrackDetailList{
        overflow: hidden;
        line-height: 55/50rem;
        width: 100%;
        display:table;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        &>div{
          flex-grow: 1;
          display:table-cell;
        }
        &>.TrackOfTime{
          text-align: center;
          padding:20/50rem 30/50rem 20/50rem 15/50rem;
          width: 170/50rem;
        }
        &>.TrackOfTime>div{
          line-height: 48/50rem;
          &:nth-child(1){
            font-size: 23/50rem;
            color: #aaa;
          }
          &:nth-child(2){
            color: #828282;
          }
        }
        &:first-child{
          &>.TrackOfTime>div{
            color: #000;
          }
          .TraceListStyle_dot div{
            display: block;
            background-color: #DC1E32;
          }
          .TraceListStyle_lineTop{
            display: none;
          }
          &>.TrackProgress{
            color: #000;
          }
        }
        &:last-child{
          .TraceListStyle_dot div{
            display: block;
          }
          .TraceListStyle_lineBottom{
            display: none;
          }
        }
        &:not(:first-child):not(:last-child) .TraceListStyle>.TraceListStyle_dot{
          border-left: 1px solid #fff;
          border-right: 1px solid #fff;
          border-top: none;
          border-bottom: none;
          &>img{
            display: block;
          }
        }
      }
    }
    .TraceListStyle_outter{
      position: relative; 
    }
    .TraceListStyle{
      &>.TraceListStyle_lineTop{
        position: absolute;
        width:4/50rem;
        height:50%;
        background-color: #aaa;
        top:0;
        left: 0;
      }
      &>.TraceListStyle_dot{
        position: absolute;
        top:50%;
        margin-top: -15.5/50rem;
        left: -15.5/50rem;
        z-index: 999;
        border-radius: 50%;
        border: 1px solid #aaa;
        background-color: #fff;
        &>div{
          width: 20/50rem;
          height: 20/50rem;
          margin: 6/50rem;
          background-color:#aaa ;
          border-radius: 50%;
          position: relative;
          display: none;
          overflow: hidden;
        }
        &>img{
          width: 34/50rem;
          height: 34/50rem;
          display: none;
        }
      }
      &>.TraceListStyle_lineBottom{
        position: absolute;
        width:4/50rem;
        height:50%;
        background-color: #aaa;
        bottom:0;
        left: 0;
      }
    }
    .TrackProgress{
      padding: 20/50rem 30/50rem 20/50rem 50/50rem;
      box-sizing: border-box;
      vertical-align: middle;
      text-align: left;
      color: #828282;
    }
    .noData{
      text-align: center;
      font-size: 30/50rem;
      width: 100%;
      position: fixed;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      div{
        margin-top: 30/50rem;
        color: #201816;
      }
      .iconfont{
        font-size: 100/50rem;
        color: #201816;
      }
    }
  }
</style>


