<template>
  <div class="Waybill">
	<header>
	  <div class="currentTask">当前任务</div>
	  <div @click="historyTask"  class="historyTask">历史任务</div>
	</header>
	<!-- <div class="waybillBox"> -->
	  <div class="SwitchTab">
	    <div :class="{selected_head : LinkStatus == 'Link_PickUp'}"  @click="LinkChoose('Link_PickUp')"><span>提货</span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_Transit'}" @click='LinkChoose("Link_Transit")'><span>中转</span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_ToSend'}" @click='LinkChoose("Link_ToSend")'><span>派件</span></div>
	  </div>
	  <div class="container" style="overflow:scroll;">
	  
	    <div class="topKong"></div>

		<mt-loadmore style="text-align: center; "  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >

		  <div class="waybillContainer">

			<!-- 配载单信息 -->
			<WaybillInfo_template  @fatherMethodloadData='fatherMethodloadData'  @isLoading_="fullscreenLoading"  v-bind:shipmentListData="shipmentListData" v-bind:LinkStatus="LinkStatus" />

			<div v-if="allLoaded && shipmentListData.length " class="nodata_label">没有更多数据</div>
			<div class="kong"></div>
			<div v-if="is_NoData" class="nodata_kong"></div>
		  </div>
		</mt-loadmore>
	  </div>
	<!-- </div> -->
	  
	<!-- 页面数据为空时 -->
	<div   v-if="is_NoData"   class="NoData" >
	  <div>
		<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
		<div>{{is_NoData_text}}</div>
	  </div>
	</div>

	<!-- 页脚 -->
	<FooterIndex/>
  </div>
</template>
<script type="text/javascript">

	// 页脚
	import FooterIndex from '../../components/footer.vue'

	// 配载单 组件
	import WaybillInfo_template from './_template_WaybillInfo'
	import $ from "jquery"
	export default{
		name:"Waybill",
		data(){
			return{
				shipmentListData:[],
		        is_NoData:false,//没有数据模块样式  是否显示
		        is_NoData_text:"没有任务",//没有数据模块样式  显示的文字
				numberPerPage: 15,//每页显示几条
				loadPage: 1 ,//当前第几页
				allLoaded:true,//上拉刷新还能上拉刷新
				autoFill:false,//
				LinkStatus:"Link_PickUp",//提货 中转 派件  
			}
		},
		components:{
		  	FooterIndex,
		  	WaybillInfo_template,//配载单信息组件
		},
		mounted(){

			var that = this;

			this.allLoaded = false;

		},
		created(){
			
			// 当前环节
			if(this.$route.query.LinkStatus){
				this.LinkStatus = this.$route.query.LinkStatus;
			}
			if(this.$store.state.Waybill.waybillLinkStatus){
				this.LinkStatus = this.$store.state.Waybill.waybillLinkStatus;
			}
			this.loadData();
		},
		methods:{
		  // 切换环节
		  LinkChoose(LinkStatus){

		  	this.loadPage = 1;
		  	this.allLoaded = false;

		    this.LinkStatus = LinkStatus;

		    this.$store.state.Waybill.waybillLinkStatus = LinkStatus;

		    this.loadData();
		  },
			// 加载页面数据
			loadData(load){

				var that = this;

				var postData = {
					currentPage: that.loadPage,//当前第几页
				    numberPerPage: that.numberPerPage,//每页显示几条
				    startDate: null,//开始时间
				    endDate: null,//结束时间
				    deliveryNoinput: null,// 送货单号
                    shipmentCodeinput: null,//  配载单号
                    clientOrderNo: null,//客户单号
				    cellphone: that.$store.state.userInfo.cellphone,//手机号   15013418360
				    transportLinks:that.LinkStatus == "Link_PickUp" ? "提货":(that.LinkStatus == "Link_Transit" ? "中转":(that.LinkStatus == "Link_ToSend" ? "派件":""))
			    };

				this.httpRequest("queryShipmentData.do",postData,function(res){

					if(load){
						if(load == "TopLoad"){

							that.$refs.loadmore.onTopLoaded();

						}else if(load == "BottomLoad"){

							that.$refs.loadmore.onBottomLoaded();
						}
					}

					//上拉加载后 数据拼接
			        if (that.loadPage > 1) {
				   
				        that.shipmentListData = that.shipmentListData.concat(res.data)
				      
			        } else {
				       
				        that.shipmentListData = res.data;

						if(res.data.length){

							that.is_NoData = false; //没有数据模块 是否显示

						}else{

							that.is_NoData_text = "没有任务"
							

							that.is_NoData = true;
						}
			        }

			        that.$store.state.Waybill.shipmentListData = that.shipmentListData;

				    // 判断是否能上拉加载
				    if (res.data.length < that.numberPerPage) {
				 
				        that.allLoaded = true;
			
				    } else {
				        
				        that.allLoaded = false;
				      
				    }
				})
			},
			// 监控子组件事件
			fatherMethodloadData(){
				this.loadData();
			},
			//导航地址出现  加载特效出现  子传父事件 
			"fullscreenLoading"(isLoad,loadingText){

				console.log(isLoad,loadingText)

				this.$emit('isLoading', isLoad,loadingText);

			},
			// 点击头部“历史任务”  跳转
			historyTask(){

				this.$router.push({
		            name:"historyList"
		        })
			},
			// 下拉刷新
			loadTop() {

		        this.loadPage = 1;

		        this.allLoaded = false;

				this.loadData("TopLoad");
			},
			// 上拉加载
			loadBottom(){

			    var that = this;

			    if (that.allLoaded){
			     	that.$refs.loadmore.onBottomLoaded();
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				this.loadData("BottomLoad");
			}
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Waybill{
	height: 100%;
  }
  header{
	background-color: #fff;
	color: #000;
	display: flex;
	&>div{
	  flex-grow: 1;
	}
	.currentTask{
	  color: @PublicColor;
	  border-bottom: 6/50rem solid @PublicColor;
	}
	.historyTask{
	  border-bottom: 6/50rem solid #fff;
	}
  }
	.SwitchTab{
	  width: 100%;
	  height: 75/50rem;
	  font-size: 32/50rem;
	  position: fixed;
	  top: 91/50rem;
	  left: 0;
	  z-index: 999;
	  background-color: #fff;
	  display: flex;
	  &>div{
		flex-grow: 1;
		height: 70/50rem;
		line-height: 70/50rem;
		text-align: center;
	  }
	  .selected_head>span{
		border-bottom: 5/50rem solid @PublicColor;
		height: 100%;
		display: inline-block;
		color: @PublicColor;
	  }
  	}
  	.container{
	  font-size: 29/50rem;
	  height: 100%;
	  padding-top: 0;
	  margin-top: 0;
	  padding:0 20/50rem 0;
	  background-color:#F4F4F4;
	  	.waybillContainer{

	  	}
	}
  
  .topKong{
    height: 186/50rem;
  }
</style>


