<template>
	<div  class="PrintExpress">
		<header><i class="iconfont icon-xiangzuo1"   @click="goprev"></i><span>打印面单</span></header>
		<div class="content-box">
	       <v-scan @scanData="scanData"  :loadingData="loadingData"></v-scan>
	    </div>
		<div class="container">
		    <div class="orderInfo" v-show='OrderInfo_show'>
		    	<div class="ExpressListHead" v-if='OrderInfoData'>订单信息</div>
		    	<div class="AbnormalItem" v-if='OrderInfoData'>
					<div class="nomalList">
						<div>
							<span>订单号：</span>
				    		<span v-clipboard:copy="OrderInfoData.omsNo"
						          v-clipboard:success="onCopy"
						          v-clipboard:error="onError">{{OrderInfoData.omsNo}}
						      <i  v-clipboard:copy="OrderInfoData.omsNo"
						        v-clipboard:success="onCopy"
						        v-clipboard:error="onError"
						        class="iconfont icon-fuzhi" ></i>
						    </span>
						</div>
						<div><span>寄件时间：</span><span>{{OrderInfoData.actualPieceTime}}</span></div>
						<div><span>月结卡号：</span><span>{{OrderInfoData.monthCardNumber}}</span></div>
						<div><span>货物名称：</span><span>{{OrderInfoData.productName}}</span></div>
					</div>
					<div class="AreaBox">
						<div>
							<div><span>发货城市：</span><span>{{OrderInfoData.issuePartyCity}}</span></div>
							<div><span>发货区县：</span><span>{{OrderInfoData.issuePartyDistricict}}</span></div>
						</div>
						<div><span>发货联系人：</span><span>{{OrderInfoData.issuePartyContact}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span>{{OrderInfoData.issuePartyTel}}</span>&nbsp;&nbsp;<i @click="callPhone(OrderInfoData.issuePartyTel)" class="iconfont icon-dianhua-copy"></i></div>
						<div><span>发货地址：</span><span>{{OrderInfoData.issuePartyAddr}}</span></div>
					</div>
					<div class="AreaBox">
						<div>
							<div><span>收货城市：</span><span>{{OrderInfoData.receivePartyCity}}</span></div>
							<div><span>收货区县：</span><span>{{OrderInfoData.receicePartyDistricict}}</span></div>
						</div>
						<div><span>收货联系人：</span><span>{{OrderInfoData.receivePartyContactName}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span>{{OrderInfoData.receivePartyPhone}}</span>&nbsp;&nbsp;<i @click="callPhone(OrderInfoData.receivePartyPhone)" class="iconfont icon-dianhua-copy"></i></div>
						<div><span>收货地址：</span><span>{{OrderInfoData.receivePartyAddr1}}</span></div>
					</div>
					<div class="nomalList">
						<div>
							<div><span>费用合计：</span><span>{{OrderInfoData.orderAmount}}</span></div>
							<div><span>运费：</span><span>{{OrderInfoData.transportFee}}</span></div>
						</div>
						<div>
							<div><span>件数：</span><span>{{OrderInfoData.orderQty}}</span></div>
							<div><span>实重：</span><span>{{OrderInfoData.displayWt}}</span></div>
						</div>
						<div>
							<div><span>计重：</span><span>{{OrderInfoData.chargeWeight}}</span></div>
							<div><span>付款方式：</span><span>{{OrderInfoData.chargeTypes}}</span></div>
						</div>
						<div>
							<div><span>附加服务：</span><span>{{OrderInfoData.insuranceFee}}</span></div>
							<div><span>声明价值：</span><span>{{OrderInfoData.valuationMoney}}</span></div>
						</div>
						<div>
							<div><span>代收货款：</span><span>{{OrderInfoData.collectPayment}}</span></div>
							<div><span>签回单：</span><span>{{OrderInfoData.returnBillFlag}}</span></div>
						</div>
						<div><span>尺寸：</span><span>{{OrderInfoData.productSize}}</span></div>
						<div><span>备注：</span><span>{{OrderInfoData.remark}}</span></div>
					</div>
					<div class="operateBtn">
						<div @click="printBtn">打印所有标签</div>
					</div>
				</div>

		    	<div class="ExpressListHead" v-if='OrderInfoDataList.length'>标签列表</div>
				<div class="AbnormalItem" v-for='(dataItem,index) in OrderInfoDataList'  :id="index"  :key='index'>
					<div class="nomalList">
						<div>
							<span>标签号：</span>
				    		<span v-clipboard:copy="dataItem.productNo"
						          v-clipboard:success="onCopy"
						          v-clipboard:error="onError">{{dataItem.productNo}}
						      <i  v-clipboard:copy="dataItem.productNo"
						        v-clipboard:success="onCopy"
						        v-clipboard:error="onError"
						        class="iconfont icon-fuzhi" ></i>
						    </span>
						</div>
					</div>
					<div class="operateBtn">
						<div @click="printBtn('item',index)">打印</div>
					</div>
				</div>
		    </div>
		    <!-- <div v-if="is_NoData" class="nodata_kong"></div> -->
		    <!-- 页面数据为空时 -->
			<div   v-if="is_NoData"   class="NoData" >
			  <div>
				<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
				<div>没有数据</div>
			  </div>
			</div>
			<div v-if="ifTips" class="msg_tips msg_tips1">
				<div class="tips_content">
					<i class="iconfont icon-chenggong1"></i>
					<span>{{tips_Msg}}</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
  import Scan from '@/components/Scan'
  import Axios from 'axios'
	export default {
		name:"PrintExpress",
		data(){
			return{
				OrderInfoData:{},//订单信息
				OrderInfoDataList:[],//标签号数组
				printData:[],//打印总数据
				is_NoData:false,//没有数据模块样式  是否显示
		        loadingData: false,
		        OrderInfo_show:false,//订单信息是否显示
		        scanLabel:'',//当前扫描的标签号
				tips_Msg:"",//提示信息
				ifTips:false,
			}
		},
	    components: {
	      'v-scan': Scan,  // 扫描组件
	    },
		mounted() {
			// try {
	  //           // 处理第三方传入的参数
			// 	if(plus.runtime.arguments != ""){
			// 		try{
			// 			var aj = JSON.parse(plus.runtime.arguments);
			// 			alert(aj)
			// 			// 处理JSON对象aj
			// 		}catch(e){
			// 			// 若传入的参数不是JSON格式字符，需处理异常情况
			// 		}
			// 	}
   //          } catch (e) {
   //          }
		},
		created(){
			var that = this;
		},
		methods:{
			// 扫描完成 调用
			scanData(data){
				this.is_NoData = false; //没有数据模块 是否显示

				var that = this;
				this.scanLabel = data;

				var postData = {
					scanLabel: data,//标签号
			    };
			    console.log(postData)

				that.$emit('isLoading', true,"拼命加载中");
      
		        Axios.post(that.HOST+"surfacePrint.do",{"params": JSON.stringify(postData)})
		      .then(function(res){
			        console.log(res)

			        that.$emit('isLoading', false);
			        
			        if(res.data.status==1){

			        	if(res.data.data.length){
			        		that.is_NoData = false; //没有数据模块 是否显示
			        	}else{
			        		that.is_NoData = true; //没有数据模块 是否显示
			        	}

			            that.OrderInfoData = res.data.data[0];
			            that.OrderInfoDataList = res.data.data[1];
			            that.printData = res.data.data;
						that.OrderInfo_show = true;

			        }else{
			          that.is_NoData = true;

			          var Msg;
			          if(res.data.Msg){

			            Msg = res.data.Msg;
			          }else {

			            Msg = "请求失败";
			          }
			          alert(Msg);
			        }
		        })
		        .catch(function(res){

			        that.$emit('isLoading', false);
			         
			        alert("请求失败");
		        })
			},
			// 打印 按钮  跳转到原生打印页面
			printBtn(ifitem,index){

				var that = this;

				var omsNo = [];

				if(ifitem && ifitem == 'item'){

					omsNo[0] = that.OrderInfoData;
					omsNo[1] = [that.OrderInfoDataList[index]];
					omsNo = JSON.stringify(omsNo)

				}else{

					omsNo =  JSON.stringify(that.printData);
				}
				
				console.log(omsNo)

	    		try{
	    			var main = plus.android.runtimeMainActivity();
		            var Intent = plus.android.importClass("android.content.Intent");
		            var intent = new Intent(main.getIntent());
		            intent.setClassName(main, "com.kaidongyuan.app.kdydriver.ui.activity.PrintActivity");
		            intent.setFlags(Intent.FLAG_ACTIVITY_CLEAR_TOP);
		            intent.putExtra("omsNo", omsNo);
		            main.startActivity(intent);
	    		}
	    		catch(error) {
	    			// alert(error)
				}

				try{
					plus.plugintest.PluginPrintIosFunction(omsNo, function( result ) {
						
						return;
					},function(result){
						return;
					});
				}
				catch(error) {
				}

			},
			// 返回上一页
			goprev(){

				this.$router.push({
					name:"Index",
				})
			},
			onCopy (){

				var that = this;
				that.ifTips = true;
				that.tips_Msg = "复制成功";

				setTimeout(function(){

					that.ifTips = false;
				},1000)
			},
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .PrintExpress{
  	overflow: hidden;
	.content-box{
    	padding-top: 90/50rem;
	}
  	.container{
  		height: 100%;
  		padding-top: 0;
  		box-sizing:border-box;
  		margin-top: 0;
		background-color:#F4F4F4;
		z-index: 2;
  		.orderInfo{
		    position: relative;
		    padding:25/50rem 20/50rem 20/50rem;
			background-color:#F4F4F4;
		    box-sizing:border-box;
		    overflow: hidden;
			.ExpressListHead{
				line-height: 70/50rem;
				text-align: center;
			}
			.AbnormalItem{
			  	background-color:#fff;
			  	border-radius:8/50rem;
				text-align: left;
				margin-bottom: 15/50rem;
				overflow: hidden;
				padding: 20/50rem;
				box-sizing:border-box;
				.nomalList{
					&>div{
						line-height: 55/50rem;
						overflow: hidden;
						&>div{
							float: left;
							width: 50%;
						}
					}
				}
				.AreaBox{
					border: 1/50rem solid #09D2B7;
					// border: 1/50rem solid @PublicColor;
					border-radius:8/50rem;
					padding:10/50rem;
					margin: 15/50rem 0;
					&>div{
						line-height: 55/50rem;
						overflow: hidden;
						&>div{
							float: left;
							width: 50%;
						}
					}
				}
				.operateBtn{	
					margin-top:20/50rem;
					display: flex;
					justify-content :  flex-end;
					div{
						flex-grow:1;
						max-width: 40%;
						margin-left: 15/50rem;
						text-align: center;
						background-color: @PublicColor;
						color: #fff;
						border-radius: 15/50rem;
						line-height: 60/50rem;
					}
					div:nth-child(1){
						margin-left:0;
					}
				}
			}
  		}
  	}
  }
.NoData{
	top: 800/50rem;
}
</style>


