<template>
	<div class="WaybillDetail">
		<header><i class="iconfont icon-xiangzuo1"   @click="goprev"></i><span>订单详情</span></header>
		<div class="container">
			<div class="waybillHead" style="margin-top:0;">配载单信息</div>
			<div  class="listHead">
			<!-- 	<div  class="firstLine">
					<span>{{shipmentListData.startCity}}</span>
					<i class="iconfont icon-jiang" ></i>
					<span>{{shipmentListData.endCity}}</span>
					<span class="addDate">{{shipmentListData.addDate}}</span>
				</div> -->
				<!-- 单号 -->
				<div>
					<span>配载单号：</span>
					<span v-clipboard:copy="shipmentListData.shipmentCode"
				          v-clipboard:success="onCopy"
				          v-clipboard:error="onError">{{shipmentListData.shipmentCode}}</span>
				          &nbsp;&nbsp;
					<i  @click='stopevent'  v-clipboard:copy="shipmentListData.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>&nbsp;&nbsp;
					<i @click="_ewm(shipmentListData.shipmentCode)" class="iconfont icon-iconfonterweima" ></i>
				</div>
				<div><span>创建时间：</span><span>{{shipmentListData.addDate}}</span></div>
				<div>
					<span>货&nbsp;&nbsp;&nbsp;量：</span>
					<span>{{shipmentListData.countDelivery}} 单 &nbsp; </span>
					<span>{{(shipmentListData.vol * 0.000001).toFixed(1)}} 方 &nbsp; </span>
					<span>{{(shipmentListData.wt * 0.000001).toFixed(1)}} 吨 &nbsp; </span>
					<!-- <span v-if='shipmentListData.upFlag == "T" && shipmentListData.caseQty'>{{parseInt(shipmentListData.caseQty)}}箱</span> -->
					<!-- <span v-if='shipmentListData.upFlag != "T"'>{{parseInt(shipmentListData.qty)}}箱</span> -->
					<!-- <span v-if="shipmentListData.upFlag == 'T' &&shipmentListData.trayQty">{{parseInt(shipmentListData.trayQty)}}托盘</span> -->
				</div>
				<div>
					<span>物流公司：</span>
					<span>{{shipmentListData.transPartyName}}</span>
				</div>
				<!-- <div><span>收 货 &nbsp;人：</span>{{shipmentListData.receivePartyName}}</div> -->
				<div v-if="shipmentListData.issueAddrLiat!==undefined&&shipmentListData.issueAddrLiat!==null&&shipmentListData.issueAddrLiat.length==1">
					<span>联 系 &nbsp;人：</span>
					<span>{{shipmentListData.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{shipmentListData.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i v-if="shipmentListData.issueAddrLiat[0].issuePhone" @click="callPhone(shipmentListData.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
				</div>
				<div v-if="shipmentListData.issueAddrLiat!==undefined&&shipmentListData.issueAddrLiat!==null&&shipmentListData.issueAddrLiat.length==1">
					<span>装货地址：</span>
					<span>{{shipmentListData.issueAddrLiat[0].issueAddr1}}</span>
				</div>
				<div  v-if="shipmentListData.issueAddrLiat!==undefined&&shipmentListData.issueAddrLiat!==null&&shipmentListData.issueAddrLiat.length !=1 &&shipmentListData.issueAddrLiat.length !=0" @click="MultiAddress(null)">
					<i class="iconfont icon-tishi" ></i>
					<span style="color:#038EEA; ">查看多装货地址</span>
				</div>
				<div v-if="shipmentListData.issueAddrLiat!==undefined&&shipmentListData.issueAddrLiat!==null&&shipmentListData.issueAddrLiat.length == 1 && shipmentListData.issueAddrLiat[0].phoneAppointTime">
					<span>预约时间：</span>
					<span>{{shipmentListData.issueAddrLiat[0].phoneAppointTime}}</span>
				</div>
				<div v-if="shipmentListData.issueAddrLiat!==undefined&&shipmentListData.issueAddrLiat!==null&&shipmentListData.issueAddrLiat.length == 1 && shipmentListData.issueAddrLiat[0].phoneAppointTime">
					<span>预约状态：</span>
					<span>{{shipmentListData.issueAddrLiat[0].appointLoadingStatus == "NEW" ? "新预约": shipmentListData.issueAddrLiat[0].appointLoadingStatus == "YI" ? "已确定" : shipmentListData.issueAddrLiat[0].appointLoadingStatus == "REFUSE" ? "已拒绝" : ""}}</span>
				</div>
			</div>

			<!-- 订单信息 -->
			<OrderInfo_template v-if="orderList.length"   v-bind:_orderInfo="orderList,historyList,LinkStatus" />
		</div>
		<!-- <div class="BTN"><div class="RoutingInfo" @click='toRoutingInfo'>查看路由</div></div> -->
		<!-- 查看多装货地址弹出框 -->
		<div class="MultiAddress">
			<div>
				<i @click="closeAddress"  class="iconfont icon-iconfontcuowu" ></i>
				<div class="addressHead">
					<span  @click="address_SwitchView(idx,'usedtemplate')"  v-for='(itemAddHead,idx) in address_listData'  :id="idx"  :key='idx' >地址{{idx+1}}</span>
				</div>
				<div class="addressContent">
					<div>
						<span>地址：</span>
						<span>{{currentAddressInfo.issueAddr1}}</span>
					</div>
					<div>
						<span>联系人：</span>
						<span>{{currentAddressInfo.issueUser}}</span>
					</div>
					<div>
						<span>电话：</span>
	 					<span>{{currentAddressInfo.issuePhone}}</span> &nbsp;&nbsp;&nbsp;&nbsp;  <i style="color:#038EEA; " @click="callPhone(currentAddressInfo.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
					</div>
					<div v-if="currentAddressInfo.phoneAppointTime">
						<span>预约时间：</span>
						<span>{{currentAddressInfo.phoneAppointTime}}</span>
					</div>
					<div v-if="currentAddressInfo.phoneAppointTime">
						<span>预约状态：</span>
						<span>{{currentAddressInfo.appointLoadingStatus}}</span>
					</div>
				</div>
			</div>
		</div>
		<div v-if="ifTips" class="msg_tips msg_tips1">
			<div class="tips_content">
				<i class="iconfont icon-chenggong1"></i>
				<span>{{tips_Msg}}</span>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	
	// 订单信息 组件
	import OrderInfo_template from "../../Waybill/WaybillInfo/_template_orderInfo"

	export default{
		name:"WaybillDetail",
		data(){
			return{
				LinkStatus:"Link_PickUp",//提货 中转 派件 
				shipmentListData:[],//页面数据  json格式
				orderList:[],//订单列表
				address_listData:[],// 当前多地址配载单的地址数组信息 shipmentListData[i].issueAddrLiat
				shipmentID:"",//配载单id
				currentAddressInfo:[],// 当前显示的地址数组信息
				historyList:true,//是否是历史任务页面跳转来
				tips_Msg:"",//提示信息
				ifTips:false
			}
		},
		components:{
			OrderInfo_template,// 订单信息 组件
		},
		created(){

			var that = this;

			if(this.$route.query.shipmentID){

				this.shipmentID = this.$route.query.shipmentID;
				
			}else{
				this.shipmentID = this.$store.state.Waybill.WaybillInfo.clickShipmentId;
			}

			// 当前环节
			if(this.$route.query.LinkStatus){
				this.LinkStatus = this.$route.query.LinkStatus;
			}
			
			var postData = {
				shipmentId: that.shipmentID,//配载单id
				appointLoadingSite:"YI",//查询已交付状态配载单
		    };

			this.httpRequest("queryDeliverDetailInfo.do",postData,function(res){

				if(res.data.length){

					if(res.data[0].length){

						that.shipmentListData = res.data[0][0];

					}else{
						that.shipmentListData = that.$store.state.search_WaybillDetail;
					}

					that.orderList = res.data[1];
					
					// that.$store.state.Waybill.shipmentListData.orderList = res.data;
					
					for(var m = 0; m < res.data[1].length; m++){

						that.orderList[m].ifDeliver = false;
					}

				}else{

					//不需要弹出框提示，页面显示“没有数据”
				}
			})
		},
		methods:{
			// 返回上一页
			goprev(){

				var that = this;
				this.$router.push({
					name:"historyList",
					query:{
						LinkStatus:that.LinkStatus
					}
				})
			},
			// 跳转到二维码页面
			_ewm(shipmentCode){
				event.stopPropagation();

				this.$router.push({
					name:"QRCode",
					query:{
						shipmentCode:shipmentCode,//配载单号
					}
				})
			},
			// 跳转到查看路由页面
			toRoutingInfo(){
				var that = this;
				this.$router.push({
					name:"RoutingInfo",
					query:{
						shipmentCode:that.shipmentListData.shipmentCode,//配载单号
					}
				})
			},
			onCopy (){

				var that = this;
				that.ifTips = true;
				that.tips_Msg = "复制成功";

				setTimeout(function(){

					that.ifTips = false;
				},1000)
			},
		}
	}
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
	.WaybillDetail{
		overflow: hidden;
		.container{
			overflow: hidden;
			&>div{
				overflow: hidden;
				position: relative;
				width: 100%;
				// padding: 20/50rem;
				box-sizing: border-box;	
				&>div{
					line-height: 55/50rem;
					overflow: hidden;
				}
				.iconfont{
					font-size: 35/50rem;
					color: @PublicColor;
					margin-right: 5/50rem;
				}
			}
			&>div.listHead{
				padding:10/50rem 16/50rem;
				box-sizing: border-box;
				&>p{
					margin-top: 10/50rem;
					display: flex;
					span{
						flex-grow:1;
						margin-left: 15/50rem;
						text-align: center;
						background-color: @PublicColor;
						color: #fff;
						border-radius: 15/50rem;
						padding: 8/50rem 0;
					}
					span:nth-child(1){
						margin-left:0;
					}
				}
				&>div{
					&.firstLine{
						span{
							float: left;
						}
						.icon-jiang{
							margin:0 15/50rem;
							float: left;
						}
						span:last-child{
							float: right;
							color: #777;
							font-size: 26/50rem;
						}
					}
				}
			}
			.waybillHead{
				height: 65/50rem;
				line-height: 65/50rem;
				background-color:#F4F4F4;
				// margin-top: 30/50rem;
				text-align: center;
				// padding: 10/50rem 20/50rem;
			}
		}
		.BTN{
			font-size: 28/50rem;
			.RoutingInfo{
				width: 230/50rem;
				height: 70/50rem;
				line-height: 70/50rem;
				position:fixed;
				bottom:30/50rem;
				left: 50%;
				transform: translateX(-50%);
				background-color:@PublicColor;
				text-align: center;
				color: #fff;
				border-radius:35/50rem;
			}
		}
	}
</style>


